<template>
  <div class="select-token">
    <div class="select-token__select-wrapper">
      <div class="select-token__select-label">Select <br /> a token</div>
      <div class="select-token__select">
        <div
          class="select-token__select-result"
          @click="selectTokenShowDropdown"
        >
          <div class="select-token__select-result-content">
            <div class="select-token__select-result-token-icon">
              <e-token-icon :contract="selectedCurrency.contract"/>
            </div>

            <div class="select-token__select-result-token">
              <div class="select-token__select-result-token-amount">
                <div class="select-token__select-result-token-fullname">{{ selectedCurrency.name }}</div>
              </div>

              <div class="select-token__select-result-token-short">
                {{ selectedCurrency.shortName }}
              </div>
            </div>
          </div>
          <e-icon :src="iconArrowDowner"/>
        </div>

        <div
          class="select-token__select-dropdown"
          v-if="isSelectTokenDropdown"
          v-click-outside="selectTokenHideDropdown"
        >
          <div class="select-token__select-dropdown-search">
            <e-icon :src="iconSearch"/>
            <input
              type="text"
              v-model="tokenSearch"
              class="select-token__select-dropdown-search-input"
              placeholder="Type token name"
              ref="tokenSearchInput"
            >
          </div>
          <div class="select-token__select-dropdown-content">
            <div
              v-for="currency of filteredCurrencies"
              :key="currency.contract"
              class="select-token__select-item"
              @click="setCurrency(currency.contract)"
            >
              <div class="select-token__select-dropdown-token-icon">
                <e-token-icon :contract="currency.contract"/>
              </div>

              <div class="select-token__select-dropdown-token-fullname">
                {{ currency.name }}
              </div>
            </div>
            <div
              v-if="filteredCurrencies.length === 0"
              class="select-token__select-dropdown-content-empty"
            >
              nothing found
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import iconSearch from '@/icons/e-icon_search.svg';
import iconArrowDowner from '@/icons/e-icon_arrow_downer.svg';

import ETokenIcon from '@/components/ETokenIcon';
import EIcon from '@/components/EIcon';
import { ETH_ADDRESS } from '@/util/constants';
import { searchTokenMixin } from '@/util/mixins';

export default {
  name: 'e-select-token',

  props: {
    currencyAddress: {
      type: String,
      default: ETH_ADDRESS,
    },
  },

  data() {
    return {
      iconSearch,
      iconArrowDowner,
      isSelectTokenDropdown: false,
      tokenSearch: '',
    };
  },

  computed: {
    selectedCurrency() {
      return this.$store.getters['tokens/tokenByContract'](this.currencyAddress);
    },

    filteredCurrencies() {
      const tokens = this.$searchTokens(this.tokenSearch, this.$store.getters['tokens/ethAndTokens']);
      return tokens.slice(0, 5);
    },
  },

  created() {
    this.$store.dispatch('tokens/loadTokens');
  },

  methods: {
    async selectTokenShowDropdown() {
      this.isSelectTokenDropdown = true;
      await this.$nextTick();
      const tokenSearchInput = this.$refs.tokenSearchInput;
      if (tokenSearchInput !== undefined) {
        tokenSearchInput.focus();
      }
    },

    setCurrency(currencyAddress) {
      this.selectTokenHideDropdown();
      this.$emit('update:currencyAddress', currencyAddress);
    },

    selectTokenHideDropdown() {
      this.isSelectTokenDropdown = false;
      this.tokenSearch = '';
    },
  },

  components: {
    ETokenIcon,
    EIcon,
  },

  mixins: [searchTokenMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.select-token {
  $this: &;

  &__select-wrapper {
    width: 100%;
    display: flex;
    align-items: center;
  }

  &__select-label {
    flex: 0 0 70px;
    padding-right: 20px;
    box-sizing: border-box;
    font-size: 13px;
    line-height: 17px;
    font-weight: 500;
    color: #fff;
    text-align: right;
  }

  &__select {
    position: relative;
    width: 100%;
    height: 65px;
    box-shadow: 0 7px 10px rgba(0, 0, 0, 0.03);
    border-radius: 5px;
    background-color: #fff;
    transition: background-color .2s ease-in-out;

    &:hover {
      background: #fcfbff;
    }
  }

  &__select-result {
    cursor: pointer;
    position: relative;

    .e-icon {
      position: absolute;
      right: 8px;
      top: calc(50% - 2px);
      width: 10px;
      height: 10px;
      margin-right: 11px;

      svg {
        fill: $color-blue;
      }
    }
  }

  &__select-result-content {
    display: flex;
    padding-right: 40px;
    height: 65px;
    box-sizing: border-box;
  }

  &__select-result-token-icon {
    display: flex;
    flex: 0 0 65px;
    align-items: center;
    justify-content: center;

    .e-tocken-icon {
      width: 30px;
      height: 30px;
    }
  }

  &__select-result-token-fullname {
    font-size: 16px;
    line-height: 12px;
    color: #1F2EE9;
    font-weight: 500;
  }

  &__select-result-token-balance {
    font-size: 14px;
    color: #0D105B;
  }

  &__select-result-token-short {
    color: #1F2EE9;
    font-size: 16px;
    font-weight: 500;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    flex: 0 0 70px;
    text-align: right;
  }

  &__select-result-token {
    flex: 1;
    display: flex;
    align-items: center;
  }

  &__select-result-token-amount {
    flex: 1;
  }


  &__select-dropdown {
    position: absolute;
    width: 100%;
    bottom: 65px;
    transform: translate(0, 100%);
    z-index: 3;
    background-color: #fff;
    box-shadow: 0 7px 10px rgba(0, 0, 0, 0.03);
    padding-top: 5px;
    border-radius: 5px;
  }

  &__select-dropdown-content {
    width: 100%;
    height: 200px;
    overflow-y: auto;
    margin: 3px 0;
    position: relative;

    &::-webkit-scrollbar {
      width: 2px;
      height: 0;
    }

    &::-webkit-scrollbar-track {
      background: $color-violet;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-thumb {
      background: $color-blue;
      border-radius: 2px;
    }
  }

  &__select-dropdown-content-empty {
    font-size: 16px;
    color: rgba(131, 132, 171, .4);
    text-transform: uppercase;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__select-item {
    display: flex;
    cursor: pointer;
    align-items: center;
    height: 65px;
    border-top: 1px solid #F3F6FF;
    transition: background-color .15s ease-in-out;

    &:hover {
      background: #F3F6FF;
    }
  }

  &__select-dropdown-token-icon {
    display: flex;
    flex: 0 0 65px;
    align-items: center;
    justify-content: center;

    .e-tocken-icon {
      width: 30px;
      height: 30px;
    }
  }

  &__select-dropdown-search {
    display: flex;
    align-items: center;
    padding: 0 18px 0 20px;

    .e-icon {
      width: 20px;
      height: 20px;

      svg {
        fill: #0123e7;
      }
    }
  }

  &__select-dropdown-search-input {
    flex: 1;
    height: 50px;
    margin-left: 18px;
    font-size: 18px;
    font-weight: 600;

    &::placeholder {
      color: rgba(131, 132, 171, 0.4);
    }
  }
}
</style>



