<template>
  <div class="the-wizard-create-new-wallet-congratulations">
    <div class="the-wizard-create-new-wallet-congratulations__copy-wrapper">
      <e-copy-text
        title="Wallet address"
        :hash="wallet.address"
        compact
        btn-name="Copy"
        inline
        colorScheme="blue"
        size="m"
      />
    </div>
    <div class="the-wizard-create-new-wallet-congratulations__copy-wrapper">
      <e-copy-text
        class="the-wizard-create-new-wallet-congratulations__key"
        title="Private Key"
        :hash="formattedPrivateKey"
        compact
        is-masked
        btn-name="Copy"
        inline
        colorScheme="blue"
        size="m"
      />
    </div>

    <div class="the-wizard-create-new-wallet-congratulations__secure">
      <div class="the-wizard-create-new-wallet-congratulations__secure-icon">
        <e-icon :src="$options.icons.iconAlertThin" />
      </div>
      <div class="the-wizard-create-new-wallet-congratulations__secure-content">
        <div class="the-wizard-create-new-wallet-congratulations__secure-headline">
          Store your private key somewhere safe.
        </div>
        <div class="the-wizard-create-new-wallet-congratulations__secure-description">
          You will not be able to regain access to your wallet if you lose it.
        </div>
      </div>
      <div class="the-wizard-create-new-wallet-congratulations__secure-button">
        <e-button
          fill="transparent-white"
          padding="secondary"
          textTransform="none"
          fontSize="16"
          fontWeight="500"
          @click="download"
        >
          Download .TXT File
        </e-button>
      </div>
    </div>

    <div class="the-wizard-create-new-wallet-congratulations__trading">
      <div class="the-wizard-create-new-wallet-congratulations__trading-content">
        <div class="the-wizard-create-new-wallet-congratulations__trading-headline">Ready for trading?</div>
        <div class="the-wizard-create-new-wallet-congratulations__trading-description">
          Deposit ETH or other ERC20 Token
        </div>
      </div>

      <div class="the-wizard-create-new-wallet-congratulations__trading-button-wrapper">
        <e-button
          fill="green-white"
          :icon="$options.icons.iconEth"
          textTransform="none"
          fontWeight="500"
          fontSize="16"
          class="the-wizard-create-new-wallet-congratulations__trading-button"
          @click="handleDeposit"
        >
          Deposit Funds
        </e-button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import { toDisplayedPrivateKey } from '@/util/crypto';
import downloadWallet from '@/util/downloadWallet';

import iconAlertThin from '@/icons/e-icon__alert-thin.svg';
import iconEth from '@/icons/e-icon__eth-logo-fill.svg';

import ECopyText from '@/components/ECopyText';
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';

/**
 * Precondition: Private Key wallet is active.
 */
export default {
  name: 'the-wizard-create-new-wallet-congratulations',

  computed: {
    ...mapGetters({
      wallet: 'wallets/activeWallet',
    }),

    formattedPrivateKey() {
      return toDisplayedPrivateKey(this.wallet.privateKey);
    },
  },

  icons: {
    iconAlertThin,
    iconEth,
  },

  methods: {
    download() {
      downloadWallet(this.wallet);
    },

    handleDeposit() {
      this.$emit('next');
    },
  },

  components: {
    ECopyText,
    EButton,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wizard-create-new-wallet-congratulations {
  $this: &;

  &__copy-wrapper {
    margin-bottom: 18px;

    .e-copy-text__head {
      flex: 0 0 120px;
    }
  }

  /* specifity war */
  &__key.e-copy-text .e-copy-text__hash {
    font-size: 15px;
  }

  &__secure {
    display: flex;
    align-items: center;
    background-image: linear-gradient(-133deg, #F3F42D 0%, #F4E12D 100%);
    margin: 0 -18px;
    padding: 20px 22px;
    box-sizing: border-box;
    border-radius: 3px;
  }

  &__secure-icon {
    width: 46px;
    height: 46px;
    margin-right: 20px;
  }

  &__secure-headline {
    font-size: 16px;
    font-weight: 500;
    line-height: 18px;
    color: #0D105B;
    padding-bottom: 4px;
  }

  &__secure-description {
    font-size: 14px;
    letter-spacing: -.25px;
    font-weight: 500;
    line-height: 16px;
    color: #0D105B;
    opacity: .5;
  }

  &__secure-button {
    margin-left: auto;

    .e-button {
      width: 205px;
      height: 57px;
      color: #010D5F;
      border-color: rgba(13, 16, 91, .4);

      &:hover, &:active {
        border-color: transparent;
      }
    }
  }

  &__trading {
    display: flex;
    margin-top: 20px;
  }

  &__trading-headline {
    font-size: 16px;
    color: #fff;
    font-weight: 500;
    line-height: 24px;
  }

  &__trading-description {
    font-size: 16px;
    color: #fff;
    font-weight: 500;
    line-height: 24px;
    opacity: .5;
  }

  &__trading-button-wrapper {
    margin-left: auto;
  }

  &__trading-button {
    width: 205px;

    .e-icon {
      width: 12px;
      height: 19px;
      margin-top: -6px;
    }
  }
}
</style>



