<template>
  <div class="the-wizard-ledger-transaction">
    <div class="the-wizard-ledger-transaction__wallet">
      <span class="the-wizard-ledger-transaction__wallet-address">
        {{ walletAddress }}
      </span>
      Connected via
      <span class="the-wizard-ledger-transaction__wallet-ledger">Ledger Nano S</span>
      <e-icon
        class="the-wizard-ledger-transaction__wallet-ledger-logo"
        :src="$options.icons.iconLedgerLogo"
      />
    </div>

    <div class="the-wizard-ledger-transaction__ledger-icon-wrapper">
      <div class="the-wizard-ledger-transaction__ledger-icon">
        <e-icon :src="$options.icons.iconLedger" />
      </div>
    </div>

    <div class="the-wizard-ledger-transaction__amount">
      <e-big-number
        :value="amount"
        :decimals="currency.decimals"
        :decimal-places="3"
      />

      <div class="the-wizard-ledger-transaction__amount-currency">
        {{ currency.shortName }}
      </div>

      <div class="the-wizard-ledger-transaction__amount-refresh">
        <e-icon :src="$options.icons.iconRefresh" />
      </div>
    </div>

    <div class="the-wizard-ledger-transaction__steps">
      <div
        v-if="tokenAddress"
        :class="{ 'is-checked': approveConfirmed }"
        class="the-wizard-ledger-transaction__step-item"
      >
        <div class="the-wizard-ledger-transaction__step-check">
          <e-icon
            v-if="approveConfirmed"
            :src="$options.icons.iconCheck"
          />
        </div>
        Approve token transfer
      </div>

      <div
        :class="{ 'is-checked': depositConfirmed }"
        class="the-wizard-ledger-transaction__step-item"
      >
        <div class="the-wizard-ledger-transaction__step-check">
          <e-icon
            v-if="depositConfirmed"
            :src="$options.icons.iconCheck"
          />
        </div>
        {{ tokenAddress ? 'Transfer token' : 'Deposit ETH' }}
      </div>
    </div>
  </div>
</template>

<script>
/**
 * Components:  Wizard,   
 *
 * @version 1.0.2
 */

import last from 'lodash/last';
import { mapGetters } from 'vuex';
import { ZERO } from '@/util/numbers';
import { DEPOSIT_OPERATION, ETH_ADDRESS } from '@/util/constants';

import iconWallet from '@/icons/e-icon__wallet.svg';
import iconLedger from '@/icons/e-icon__ledger-illustration.svg';
import iconLedgerLogo from '@/icons/e-icon__ledger-logo-small.svg';
import iconRefresh from '@/icons/e-icon__refresh.svg';
import iconCheck from '@/icons/e-icon__check.svg';

import EBigNumber from '@/components/EBigNumber';
import EIcon from '@/components/EIcon';

const TIMEOUT_SHOW_DEPOSIT_CONFIRMED = 2500;

export default {
  name: 'the-wizard-ledger-transaction',

  data() {
    return {
      amount: ZERO,
      /**
       * null for ETH
       */
      tokenAddress: null,
      transactionHash: null,
    };
  },

  computed: {
    ...mapGetters({
      walletAddress: 'wallets/activeWalletAddress',
    }),

    latestDeposit() {
      const operations = this.$store.getters['deposit/newAndPendingOperations'];
      const deposits = operations.filter(o => o.type === DEPOSIT_OPERATION);
      return last(deposits) || null;
    },

    latestDepositTransactionHash() {
      const deposit = this.latestDeposit;
      return (deposit && deposit.transactionHash) || null;
    },

    currency() {
      if (this.tokenAddress) {
        return this.$store.getters['tokens/tokenByContractSimple'](this.tokenAddress);
      }
      return {
        shortName: 'ETH',
        decimals: 18,
      };
    },

    approveConfirmed() {
      const deposit = this.latestDeposit;
      return !!(deposit && deposit.approveHash);
    },

    depositConfirmed() {
      const deposit = this.latestDeposit;
      return !!(deposit && deposit.transactionHash);
    },

    canceled() {
      const deposit = this.latestDeposit;
      if (deposit) {
        const latestHash = deposit.transactionHash;
        return latestHash != null && latestHash !== this.transactionHash;
      }
      return true;
    },
  },

  watch: {
    depositConfirmed: {
      immediate: true,
      handler(confirmed) {
        if (confirmed) {
          setTimeout(() => {
            if (!this.isDestroyed) {
              this.$emit('confirmed');
            }
          }, TIMEOUT_SHOW_DEPOSIT_CONFIRMED);
        }
      },
    },

    latestDepositTransactionHash: {
      immediate: true,
      handler(hash) {
        if (hash) {
          this.transactionHash = hash;
        }
      },
    },

    canceled: {
      immediate: true,
      handler(canceled) {
        if (canceled) {
          this.$emit('canceled');
        }
      },
    },
  },

  created() {
    const deposit = this.latestDeposit;
    if (deposit) {
      this.amount = deposit.value;
      this.tokenAddress = deposit.tokenAddress === ETH_ADDRESS ? null : deposit.tokenAddress;
    }
  },

  mounted() {
    this.$store.dispatch('deposit/listenToBalanceOperations');
    this.$store.dispatch('balance/listenToPrivateBalanceChanges');
    this.$store.dispatch('balance/listenToContractBalanceChanges');
    this.$store.dispatch('blockchain/listenToGasPriceChanges');
  },

  beforeDestroy() {
    this.$store.dispatch('blockchain/stopListeningToGasPriceChanges');
    this.$store.dispatch('deposit/stopListeningToBalanceOperations');
    this.$store.dispatch('balance/stopListeningToPrivateBalanceChanges');
    this.$store.dispatch('balance/stopListeningToContractBalanceChanges');
    this.isDestroyed = true;
  },

  icons: {
    iconWallet,
    iconLedger,
    iconLedgerLogo,
    iconRefresh,
    iconCheck,
  },

  components: {
    EBigNumber,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wizard-ledger-transaction {
  $this: &;

  padding: 0 0 5px;

  &__wallet {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    letter-spacing: -.5px;
  }

  &__wallet-address {
    color: #00E997;
    padding-right: 6px;
  }

  &__wallet-ledger {
    color: #BBC1DD;
    text-transform: uppercase;
  }

  &__wallet-ledger-logo {
    fill: #BCC1DB;
    margin: 2px 0 0 6px;
    width: 22px;
    height: 22px;
  }

  &__ledger-icon-wrapper {
    margin: 24px 0 20px;
    position: relative;
    display: flex;
    justify-content: center;
  }

  &__ledger-icon {
    width: 392px;
    height: 110px;
  }

  &__ledger-icon-count  {
    width: 24px;
    height: 24px;
    background: #00E997;
    border-radius: 50%;
    border: 4px solid #187adb;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #0D105B;
    font-size: 14px;
    font-weight: 500;
    position: absolute;
    bottom: 0;
    left: calc(50% + 42px);
    transform: translate(-50%, 2px);
  }

  &__amount {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #00E997;
    font-size: 26px;
    font-weight: 500;
    letter-spacing: -.5px;
  }

  &__amount-currency {
    padding-left: 6px;
  }

  &__amount-refresh {
    width: 20px;
    height: 15px;
    margin-left: 8px;
    cursor: pointer;

    svg path {
      fill: #00E997;
    }
  }

  &__steps {
    width: 230px;
    margin: 6px auto 0;
  }

  &__step-item {
    display: flex;
    align-items: center;
    color: #fff;
    font-size: 19px;
    font-weight: 500;
    line-height: 24px;
    height: 40px;

    &.is-checked {
      color: rgba(41,231,167,.4);

      #{$this}__step-check {
        background: #167C88;
        border-color: #167C88;
      }
    }
  }

  &__step-check {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 2px solid rgba(61, 238, 188, .4);
    box-sizing: border-box;
    margin-right: 10px;

    .e-icon {
      margin: -1px 0 0 4px;
    }
  }
}
</style>



