<template>
  <div class="the-wizard-status">
    <div class="the-wizard-status__heading" :class="[ confirmed ? 'is-confirmed' : 'is-progress' ]">
      <div class="the-wizard-status__heading-icon">
        <e-icon
          v-if="confirmed"
          :src="$options.icons.iconCheck"
        />
        <e-icon
          v-else
          :src="$options.icons.iconRefresh"
        />
      </div>

      <div class="the-wizard-status__heading-title">
        {{ confirmed ? 'Deposit Confirmed!' :'Depositing to Contract' }}
      </div>
    </div>

    <div class="the-wizard-status__wallet">
      <div class="the-wizard-status__wallet-amount">
        <e-icon :src="$options.icons.iconWallet" />
        <e-big-number
          :value="amount"
          :decimals="currency.decimals"
          :decimal-places="9"
        />
        <div class="the-wizard-status__wallet-amount-shortname">
          {{ currency.shortName }}
        </div>
      </div>

      <a
        rel="nofollow noreferrer"
        target="_blank"
        :href="`${ETHERSCAN_URL}/tx/${transactionHash}`"
        class="the-wizard-status__wallet-link"
        :class="{ 'the-wizard-status__wallet-link_disabled': !transactionHash }"
      >
        <e-icon :src="$options.icons.iconLink" />
        {{ transactionHash }}
      </a>
    </div>

    <div class="the-wizard-status__buttons">
      <e-button
        fill="green-white"
        textTransform="none"
        fontWeight="500"
        fontSize="18"
        class="the-wizard-status__trading-button"
        @click="goToTrading"
      >
        Go to trading
      </e-button>
    </div>
  </div>
</template>

<script>
import last from 'lodash/last';
import { DEPOSIT_OPERATION, ETH_ADDRESS } from '@/util/constants';
import { ZERO } from '@/util/numbers';

import iconWallet from '@/icons/e-icon__wallet2.svg';
import iconRefresh from '@/icons/e-icon__refresh.svg';
import iconCheck from '@/icons/e-icon__check.svg';
import iconLink from '@/icons/e-icon__link.svg';

import EBigNumber from '@/components/EBigNumber';
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';

/**
 * Precondition: last deposit
 */
export default {
  name: 'the-wizard-status',

  data() {
    return {
      amount: ZERO,
      transactionHash: '',
      /**
       * null for ETH
       */
      tokenAddress: null,
    };
  },

  computed: {
    confirmed() {
      const hash = this.transactionHash;
      return hash && !this.$store.getters['deposit/newAndPendingOperations'].some(o => o.transactionHash === hash);
    },

    latestTransactionHash() {
      const deposit = this.latestDeposit;
      if (deposit) {
        return deposit.transactionHash;
      }
      return null;
    },

    latestDeposit() {
      const operations = this.$store.getters['deposit/newAndPendingOperations'];
      const deposits = operations.filter(o => o.type === DEPOSIT_OPERATION);
      return last(deposits) || null;
    },

    currency() {
      if (this.tokenAddress) {
        return this.$store.getters['tokens/tokenByContractSimple'](this.tokenAddress);
      }
      return {
        shortName: 'ETH',
        decimals: 18,
      };
    },
  },

  watch: {
    latestTransactionHash(latest) {
      if (!this.transactionHash) {
        this.transactionHash = latest;
      }
    },
  },

  created() {
    const deposit = this.latestDeposit;
    this.amount = deposit.value;
    this.tokenAddress = deposit.tokenAddress === ETH_ADDRESS ? null : deposit.tokenAddress;
    const hash = deposit.transactionHash;
    if (hash) {
      this.transactionHash = hash;
    }
  },

  icons: {
    iconWallet,
    iconRefresh,
    iconCheck,
    iconLink,
  },

  methods: {
    goToTrading() {
      if (this.tokenAddress) {
        this.$router.push({
          name: 'token',
          params: { token: this.tokenAddress },
        });
      } else {
        this.$router.push({ name: 'tokens' });
      }
    },
  },

  components: {
    EBigNumber,
    EButton,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wizard-status {
  $this: &;

  padding: 0 0 34px;

  &__heading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 40px 0 32px;

    &.is-progress {
      #{$this}__heading-icon {
        border-color: #F4E12D;

        svg path {
          fill: #F4E12D;
        }

        .e-icon {
          animation: rotating 1.8s linear infinite;
        }
      }
      #{$this}__heading-title {
        color: #F4E12D;
      }
    }

    &.is-confirmed {
      #{$this}__heading-icon {
        border-color: #00E997;

        .e-icon__check-fill {
          stroke: #00E997;
        }
      }
      #{$this}__heading-title {
        color: #00E997;
      }
    }
  }

  &__heading-icon {
    width: 46px;
    height: 46px;
    border-radius: 50%;
    border: 2px solid transparent;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;

    .e-icon {
      width: 20px;
      height: 15px;
    }
  }

  &__heading-title {
    font-size: 26px;
    line-height: 24px;
    font-weight: 500;
    margin-top: 22px;
  }

  &__wallet-amount {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 19px;
    font-weight: 500;
    letter-spacing: -.5px;
    color: #fff;

    .e-icon {
      width: 29px;
      height: 26px;
      margin-right: 8px;

      .e-icon__wallet-colored {
        fill: #275379;
      }
    }

    .e-big-number {
      .e-big-number__main-lasts {
        opacity: .4;
      }
    }
  }

  &__wallet-amount-shortname {
    opacity: .4;
    padding-left: 4px;
  }

  &__wallet-link_disabled {
    visibility: hidden;
  }

  &__wallet-link {
    height: 24px;
    text-decoration: none;
    padding-top: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: -.5px;
    color: rgba(129, 168, 247, .5);
    transition: color .2s ease-in-out;

    &:hover {
      color: #fff;
    }

    .e-icon {
      width: 15px;
      height: 15px;
      margin-right: 10px;
    }
  }

  &__buttons {
    padding-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__trading-button {
    width: 244px;
  }

  @keyframes rotating {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
}
</style>



