<template>
  <div class="the-main-faq-item" v-click-outside="onClose">
    <div class="the-main-faq-item__list-item-main" :class="{ 'is-open': isOpen }" @click="toggleOpen">
      <slot name="question"></slot>
      <div class="the-main-faq-item__list-item-arrow">
        <e-icon :src="iconArrowDowner" />
      </div>
    </div>
    <div class="the-main-faq-item__list-item-response" :class="{ 'is-open': isOpen }">
      <slot name="response"></slot>
    </div>
  </div>
</template>

<script>
import iconArrowDowner from '@/icons/e-icon_arrow_downer.svg';

import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';

export default {
  name: 'the-main-news',

  data() {
    return {
      iconArrowDowner,
      isOpen: false,
    };
  },

  methods: {
    toggleOpen() {
      this.isOpen = !this.isOpen;
    },

    onClose() {
      this.isOpen = false;
    },
  },

  components: {
    EButton,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-main-faq-item {
  $this: &;
  position: relative;

  &__list-item-arrow {
    width: 12px;
  }

  &__list-item-main {
    cursor: pointer;
    position: relative;
    width: 100%;
    height: 75px;
    border-radius: 5px;
    box-shadow: 0 12px 24px rgba(0,0,0,.04);
    color: #0D105B;
    font-size: 17px;
    font-weight: 500;
    display: flex;
    align-items: center;
    padding: 0 50px 0 28px;
    box-sizing: border-box;
    background: #fff;
    transition: all .15s ease-in-out;

    &:hover {
      background-color: #f5f4f4;
      color: #1F2EE9;
    }

    &.is-open {
      font-family: 'DIN Pro';
      font-weight: 600;
      background-color: #f5f4f4;
      color: #0d105b;

      #{$this}__list-item-arrow {
        transform: rotate(180deg) translate(0, -7px);
      }
    }
  }

  &__list-item-arrow {
    position: absolute;
    right: 28px;
    top: calc(50% - 3px);

    svg {
      fill: #0123E7;
    }
  }

  &__list-item-response {
    margin-top: -7px;
    border-top: 1px solid #CFE2F5;
    background-color: #f5f4f4;
    padding: 20px 28px;
    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
    border-radius: 0 0 5px 5px;
    opacity: 0;
    position: absolute;
    z-index: 1;
    right: -9999px;
    font-size: 17px;
    font-weight: 500;
    line-height: 25px;
    color: #000;
    transition: opacity .15s ease-in-out, transform .15s ease-in-out;

    &.is-open {
      right: auto;
      opacity: 1;
      transform: translate(0, 3px);
    }
  }
}
</style>



