<script>
/**
 * Component:   
 *
 * @version 2.0.0
 */


import TheTokenSearchEtherscanMobile from '@/containers/blocks/TheTokenSearchEtherscanMobile';
import doScrollingSimple from '@/util/doScrollingSimple';
import { mapGetters, mapState } from 'vuex';
import getIntercom from '@/util/getIntercom';
import { chatMixin, routesMixin } from '@/util/mixins';
import delay from 'delay';

// Components
import EBigNumber from '@/components/EBigNumber';
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';
import TheFooter from '@/containers/blocks/TheFooter';
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheHeaderNew from '@/containers/blocks/TheHeaderNew';
import TheMainFaq from '@/containers/blocks/TheMainFaq';
import TheMainInvesting from '@/containers/blocks/TheMainInvesting';
import TheMainNews from '@/containers/blocks/TheMainNews';
import TheMenuSidebar from '@/containers/blocks/TheMenuSidebar';
import ThePopup from '@/containers/blocks/ThePopup';
import TheWizard from '@/containers/blocks/TheWizard';
import TheMobileTokenSearch from '@/containers/blocks/TheMobileTokenSearch';
import TheTokenSearchResultNew from '@/containers/blocks/TheTokenSearchResultNew';

// Icons
import iconQuestion from '@/icons/e-icon_question.svg';
import iconCircleCheck from '@/icons/e-icon__circle-check.svg';
import iconEth from '@/icons/e-icon__eth-raw.svg';
import iconGithubRound from '@/icons/e-icon__logo-github-round.svg';
import iconTwitter from '@/icons/e-icon_twitter.svg';
import iconTelegram from '@/icons/e-icon_telegram.svg';
import iconChat from '@/icons/e-icon__chat.svg';
import iconSocialPointers from '@/icons/e-icon__social-pointers.svg';
import iconSocialPointersMobile from '@/icons/e-icon__social-pointers-mobile.svg';
import iconMouse from '@/icons/e-icon__mouse.svg';
import iconSearch from '@/icons/e-icon_search.svg';
import iconExchange from '@/icons/e-icon__exchange.svg';


// TODO: wait TheMobileTokenSearch (mobile only)
// TODO: code-splitting for mobile & desktop
// TODO: do something with TheHeaderInner

export default {
  name: 'page-index-new',

  data() {
    return {
      // search
      searchValue: '',
      isDesktopSearchFocused: false,
      isMobileSearchActive: false,

      // Icons
      iconQuestion,
      iconCircleCheck,
      iconEth,
      iconGithubRound,
      iconTwitter,
      iconTelegram,
      iconChat,
      iconSocialPointers,
      iconSocialPointersMobile,
      iconMouse,
      iconSearch,
      iconExchange,
    };
  },

  computed: {
    ...mapState({
      volumeTotal: state => state.trades.volumeTotal,
    }),

    ...mapGetters({
      isPopupShowing: 'ui/isPopupShowing',
      totalWallets: 'wallets/totalWallets',
      windowSize: 'ui/windowSize',
      firstContractHash: 'futuresActiveContract/firstContractHash',
    }),

    loadFinished() {
      return (
        this.$store.getters['trades/isVolumesInitSuccess'] &&
        this.$store.state.wallets.totalWalletsLoaded &&
        this.$store.state.tokens.tokensLoaded
      );
    },

    isMobileScreenSize() {
      return this.windowSize.width < 992;
    },
  },

  watch: {
    isMobileScreenSize(isNextSizeMobile, isPrevSizeMobile) {
      if (isPrevSizeMobile && !isNextSizeMobile && this.isMobileSearchActive) {
        const searchInput = this.$refs.search;
        if (searchInput) {
          searchInput.focus();
        }
      }
      this.isMobileSearchActive = isNextSizeMobile && (this.isMobileSearchActive || this.isDesktopSearchFocused);
    },
  },

  async mounted() {
    this.init();
    this.$ga.page(this.$router);
    this.$enableChat();
  },

  methods: {
    /**
     *     
     *
     * @returns {Promise<void>}
     */
    async init() {
      this.$store.dispatch('trades/loadVolumes');
      this.$store.dispatch('trades/listenToTradeVolumesChange');
      this.$store.dispatch('wallets/loadTotalWallets');
      this.$store.dispatch('tokens/loadTokens');
    },

    handleTokenSelect(item) {
      this.$router.push({ name: 'token', params: { token: item.contract } });
    },

    handleSearchFocus() {
      if (this.isMobileScreenSize) {
        this.isMobileSearchActive = true;
      } else {
        this.isDesktopSearchFocused = true;
      }
    },

    async handleSearchBlur() {
      await delay(150); // TODO: workaround with aditional variable
      this.searchValue = '';
      this.isDesktopSearchFocused = false;
    },

    closeMobileSearch() {
      this.isMobileSearchActive = false;
    },

    showChat() {
      getIntercom()('show');
    },

    scrollDown() {
      const wizard = this.$refs.wizard;
      if (wizard) {
        const contentWrap = this.$refs.contentWrap;
        const wizardRootEl = wizard.$el;
        if (contentWrap && wizardRootEl) {
          doScrollingSimple(contentWrap, wizardRootEl);
        }
      }
    },

    depositWizard() {
      const wizard = this.$refs.wizard;
      if (wizard) {
        const contentWrap = this.$refs.contentWrap;
        const wizardRootEl = wizard.$el;
        if (contentWrap && wizardRootEl) {
          doScrollingSimple(contentWrap, wizardRootEl);
        }
      }
    },
  },

  mixins: [chatMixin, routesMixin],

  components: {
    TheTokenSearchEtherscanMobile,
    TheMobileTokenSearch,
    TheTokenSearchResultNew,
    EBigNumber,
    EButton,
    EIcon,
    TheFooter,
    TheHeaderInner,
    TheHeaderNew,
    TheMainFaq,
    TheMainInvesting,
    TheMainNews,
    TheMenuSidebar,
    ThePopup,
    TheWizard,
  },
};
</script>

