    left: calc(10% + 50px);
    animation: falling 4s 2s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-7 {
    top: calc(80% + 160px);
    left: calc(2% + 100px);
    animation: falling 3.5s 2s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-8 {
    top: 60%;
    left: 8%;
    animation: falling 3.5s .5s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-9 {
    top: 50%;
    left: 2%;
    animation: falling 4.5s 1s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-10 {
    top: 70%;
    left: 200px;
    animation: falling 3.5s .5s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-11 {
    top: 70%;
    left: 0;
    animation: falling 4.5s .5s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-12 {
    top: 85%;
    left: 60px;
    animation: falling 5s 1s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-13 {
    top: 40%;
    left: 20%;
    animation: falling 3.5s 2s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-14 {
    top: 100px;
    left: 400px;
    animation: falling 3.5s 2s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-15 {
    top: 70%;
    left: 5%;
    animation: falling 3.5s .5s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-16 {
    top: 80%;
    left: 10%;
    animation: falling 3.5s 1.5s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-17 {
    top: 85%;
    left: calc(8% + 60px);
    animation: falling 4.5s 1s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-18 {
    top: 60%;
    left: 5%;
    animation: falling 3.5s 4.5s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-19 {
    top: 70%;
    left: 15%;
    animation: falling 4s 5.5s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-20 {
    top: 200px;
    left: calc(5% + 110px);
    animation: falling 3s 5s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-21 {
    top: 50%;
    left: calc(20% - 140px);
    animation: falling 3s .1s infinite;
    animation-timing-function: linear;
  }

  &__main-comet-22 {
    top: calc(50% + 100px);
    left: calc(40% - 200px);
    animation: falling 5s 4.5s infinite;
    animation-timing-function: linear;
  }
}

@keyframes falling {
  0% {
    transform: translate3d(0,0,0) rotate(-45deg);
    opacity: 0;
  }

  15% {
    transform: translate3d(112px,-112px,0) rotate(-45deg);
    opacity: 1;
  }

  70% {
    transform: translate3d(450px, -450px,0) rotate(-45deg);
    opacity: 0;
  }
  100% {
    transform: translate3d(900px, -900px,0) rotate(-45deg);
    opacity: 0;
  }
}

</style>



