<template>
  <div class="the-wizard">
    <div class="the-wizard__wrapper">

      <div class="the-wizard__header">
        Start your Trading Experience in <span class="the-wizard__highlight">3 Steps</span>
      </div>

      <div class="the-wizard__steps">
        <div
          class="the-wizard__step-item"
          :class="{
            'is-active': !isWalletSelected,
            'is-prev': isWalletSelected,
          }"
        >
          <e-icon
            v-if="isWalletSelected"
            class="the-wizard__step-check"
            :src="$options.icons.iconCheck"
          />
          <div class="the-wizard__step-item-label">Step 1</div>
          <div class="the-wizard__step-item-name">Create or Connect a ETH Wallet</div>
        </div>

        <div
          class="the-wizard__step-item"
          :class="{
            'is-active': isWalletSelected,
          }"
        >
          <div class="the-wizard__step-item-label">Step 2</div>
          <div class="the-wizard__step-item-name">Deposit ETH to DMEX</div>
      </div>

        <div class="the-wizard__step-item">
          <div class="the-wizard__step-item-label">Step 3</div>
          <div class="the-wizard__step-item-name">Trade. Limitless.</div>
        </div>
      </div>

      <!-- START: Step 1 -->
      <the-wizard-step-wallet
        v-if="!isWalletSelected"
        @update:selected-wallet-option="handleWalletSelection"
      />
      <!-- END: Step 1 -->

      <!-- START: Step 2 -->
      <the-wizard-step-deposit
        v-else
        :selected-wallet-option="selectedWalletOption"
        @back="selectedWalletOption = null"
      />
      <!-- END: Step 2 -->
    </div>
  </div>
</template>

<script>
import TheWizardStepDeposit from '@/containers/blocks/TheWizardStepDeposit';
import TheWizardStepWallet from '@/containers/blocks/TheWizardStepWallet';

import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';

import iconCheck from '@/icons/e-icon__check.svg';

export default {
  name: 'the-wizard',

  data() {
    return {
      selectedWalletOption: null,
    };
  },

  computed: {
    /**
     * Is step 1
     */
    isWalletSelected() {
      return this.selectedWalletOption !== null;
    },
  },

  icons: {
    iconCheck,
  },

  methods: {
    handleWalletSelection({ option }) {
      this.selectedWalletOption = option;
    },
  },

  components: {
    TheWizardStepDeposit,
    TheWizardStepWallet,
    EButton,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wizard {
  $this: &;

  padding: 60px 0 120px;
  background: #031472;
  position: relative;
  overflow: hidden;

  &::after {
    content: '';
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    z-index: 0;
    top: -100px;
    width: 100%;
    max-width: 1600px;
    height: 180px;
    background: #031576;
    border-radius: 50%;
    filter: blur(20px);
  }

  &__wrapper {
    width: 100%;
    max-width: 1310px;
    padding: 0 30px;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;
    z-index: 1;

    @include r(640) {
      padding: 0 15px;
    }
  }

  &__header {
    font-size: 36px;
    font-weight: 500;
    letter-spacing: -.5px;
    color: #fff;
    padding-bottom: 38px;
    text-align: center;
  }

  &__highlight {
    color: #00E597;
  }

  &__steps {
    display: flex;
    border-bottom: 2px solid rgba(255,255,255,.07);
    margin-bottom: 40px;
  }

  &__step-item {
    flex: 1 1 0;
    padding-bottom: 35px;
    position: relative;
    text-align: center;

    &.is-active {
      &:after {
        content: '';
        display: block;
        width: 100%;
        height: 2px;
        position: absolute;
        left: 0;
        bottom: -2px;
        background: linear-gradient(to right, rgba(0,165,108,0) 0%,rgba(3,255,182,1) 50%,rgba(0,165,108,0) 100%);
      }

      &:before {
        content: '';
        display: block;
        position: absolute;
        z-index: 1;
        bottom: -9px;
        left: calc(50% - 9px);
        width: 15px;
        height: 15px;
        background: #03146F;
        border: 5px solid #03EDAD;
        border-radius: 50%;
      }

      #{$this}__step-item-label,
      #{$this}__step-item-name {
        color: #29E7A7;
        opacity: 1;
      }
    }

    &.is-prev {
      #{$this}__step-check {
        position: absolute;
        left: calc(50% - 9px + 3px);
        bottom: -9px + 3px;
        right: 0;
        width: 18px;
        height: 15px;
        z-index: 2;
      }

      &:after {
        content: '';
        display: block;
        width: 100%;
        height: 2px;
        position: absolute;
        left: 0;
        bottom: -2px;
        background: linear-gradient(to right, rgba(0,165,108,0) 0%, rgba(3,255,182,0.5) 50%, rgba(0,165,108,0) 100%);
        transition: $transition-time;
      }

      &:before {
        content: '';
        display: block;
        position: absolute;
        z-index: 1;
        bottom: -9px;
        left: calc(50% - 9px);
        width: 18px;
        height: 18px;
        background: #167C88;
        border-radius: 50%;
        transition: $transition-time;
      }

      #{$this}__step-item-label {
        color: rgba(0, 233, 151, 0.5);
        opacity: 1;
        transition: $transition-time;
      }

      #{$this}__step-item-name {
        color: rgba(41, 231, 167, 0.5);
        opacity: 1;
        transition: $transition-time;
      }
    }
  }

  &__step-item-label {
    text-transform: uppercase;
    font-size: 15px;
    letter-spacing: -.25px;
    font-weight: 500;
    color: #81A8F7;
    opacity: .5;
    padding-bottom: 2px;
    transition: color .15s ease-in-out, opacity .15s ease-in-out;
  }

  &__step-item-name {
    font-size: 19px;
    line-height: 30px;
    font-weight: 500;
    color: #fff;

    @include r(900) {
      font-size: 16px;
      line-height: 18px;
    }
  }
}
</style>



