<template>
  <div class="the-wizard-deposit-connected">
    <div class="the-wizard-deposit-connected__wallet">
      <span class="the-wizard-deposit-connected__wallet-address">
        {{ wallet.address }}
      </span>
      Connected
      <template v-if="isMetamask">
        via
        <span class="the-wizard-deposit-connected__via-metamask">
          MetaMask
          <span class="the-wizard-deposit-connected__via-metamask-icon">
            <e-icon :src="$options.icons.iconMetamask"/>
          </span>
        </span>
      </template>
      <template v-else-if="isLedger">
        via
        <span class="the-wizard-deposit-connected__via-ledger">
          Ledger Nano S
          <span class="the-wizard-deposit-connected__via-ledger-icon">
            <e-icon :src="$options.icons.iconLedger"/>
          </span>
        </span>
      </template>
    </div>

    <div class="the-wizard-deposit-connected__select-wrapper">
      <e-select-token :currency-address.sync="currencyAddress"/>
    </div>

    <div class="the-wizard-deposit-connected__wallets-wrapper">
      <div class="the-wizard-deposit-connected__wallet-item">
        <div
          v-if="isImported"
          class="the-wizard-deposit-connected__wallet-icon">
          <e-icon :src="$options.icons.iconWallet" />
        </div>
        <div
          v-else-if="isMetamask"
          class="the-wizard-deposit-connected__wallet-icon the-wizard-deposit-connected__wallet-icon-metamask"
        >
          <e-icon :src="$options.icons.iconMetamask" />
        </div>
        <div
          v-else-if="isLedger"
          class="the-wizard-deposit-connected__wallet-icon the-wizard-deposit-connected__wallet-icon-ledger"
        >
          <e-icon :src="$options.icons.iconLedger"/>
        </div>

        <div class="the-wizard-deposit-connected__wallet-amount">
          <div class="the-wizard-deposit-connected__wallet-value">
            <e-big-number
              :value="privateBalance"
              :decimals="currency.decimals"
              :min-decimal-places="DEFAULT_MIN_DECIMALS"
              :max-decimal-places="DEFAULT_MAX_DECIMALS"
            />
            <div class="the-wizard-deposit-connected__wallet-value-currency">
              {{ currency.shortName }}
            </div>
          </div>

          <div class="the-wizard-deposit-connected__wallet-label">
            {{ isImported ? 'Private Balance' :
               isMetamask ? 'MetaMask Balance' :
               isLedger ? 'Ledger Balance' :
               ''
            }}
          </div>
        </div>
      </div>

      <div class="the-wizard-deposit-connected__wallet-item">
        <div class="the-wizard-deposit-connected__wallet-icon the-wizard-deposit-connected__wallet-icon-contract">
          <e-icon :src="$options.icons.iconWallet" />
        </div>

        <div class="the-wizard-deposit-connected__wallet-amount">
          <div class="the-wizard-deposit-connected__wallet-value">
            <e-big-number
              :value="contractBalance"
              :decimals="currency.decimals"
              :min-decimal-places="DEFAULT_MIN_DECIMALS"
              :max-decimal-places="DEFAULT_MAX_DECIMALS"
            />
            <div class="the-wizard-deposit-connected__wallet-value-currency">
              {{ currency.shortName }}
            </div>
          </div>
          <div class="the-wizard-deposit-connected__wallet-label">Contract Balance</div>
        </div>
      </div>
    </div>
    <div class="the-wizard-deposit-connected__deposit">
      <div class="the-wizard-deposit-connected__deposit-label">
        <div class="the-wizard-deposit-connected__deposit-label-simple">Amount</div>
        <div
          class="the-wizard-deposit-connected__deposit-label-all"
          @click="setDepositMax"
        >
          DEPOSIT ALL
        </div>
      </div>

      <div class="the-wizard-deposit-connected__deposit-input">
        <e-input-number
          v-model="deposit"
          :decimals="currency.decimals"
          :measurement-unit="currency.shortName"
          wide
        />

        <div class="the-wizard-deposit-connected__deposit-fee">
          Transaction fee:
          <div class="the-wizard-deposit-connected__deposit-fee-amount">
            <e-big-number
              :value="fee"
              :decimals="ETH_DECIMALS"
              :decimal-places="6"
            />
            ETH
          </div>
        </div>
      </div>
      <div class="the-wizard-deposit-connected__deposit-submit">
        <e-button
          fill="green-white"
          textTransform="none"
          fontWeight="500"
          fontSize="16"
          class="the-wizard-deposit-connected__deposit-button"
          @click="submit"
        >
          Deposit {{ currency.shortName }}
        </e-button>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * Components:  Wizard,   
 *
 * @version 1.0.2
 */
import { BigNumber } from 'bignumber.js';
import { mapGetters } from 'vuex';
import { validationMixin } from 'vuelidate';
import { required, decimal } from 'vuelidate/lib/validators';
import { maxDecimalPlaces, greaterThanZero } from '@/util/validators';
import { ETH_ADDRESS } from '@/util/constants';
import { ZERO } from '@/util/numbers';

import EInputNumber from '@/components/EInputNumber';
import ESelectToken from '@/components/ESelectToken';
import EBigNumber from '@/components/EBigNumber';
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';

import iconWallet from '@/icons/e-icon__wallet2.svg';
import iconMetamask from '@/icons/e-icon_metamask-normal.svg';
import iconLedger from '@/icons/e-icon__ledger-logo-small.svg';

/**
 * Precondition: Private Key or MetaMask wallet is active.
 */
export default {
  name: 'the-wizard-deposit-connected',

  data() {
    return {
      deposit: '',
      currencyAddress: ETH_ADDRESS,
    };
  },

  computed: {
    ...mapGetters({
      wallet: 'wallets/activeWallet',
      isImported: 'wallets/isActivePrivateKey',
      isMetamask: 'wallets/isActiveMetamask',
      isLedger: 'wallets/isActiveLedger',
    }),

    isEth() {
      return this.currencyAddress === ETH_ADDRESS;
    },

    currency() {
      return this.$store.getters['tokens/tokenByContract'](this.currencyAddress);
    },

    privateBalance() {
      return this.isEth
        ? this.$store.getters['balance/privateEthAvailable']
        : this.$store.getters['balance/getPrivateBalanceFor'](this.currencyAddress);
    },

    contractBalance() {
      return this.isEth
        ? this.$store.getters['balance/contractEthAvailable']
        : this.$store.getters['balance/getContractBalanceFor'](this.currencyAddress);
    },

    fee() {
      return this.isEth
        ? this.$store.getters['deposit/ethDepositGasFee']
        : this.$store.getters['deposit/tokenDepositGasFee'];
    },
  },

  mounted() {
    this.$store.dispatch('deposit/listenToBalanceOperations');
    this.$store.dispatch('balance/listenToPrivateBalanceChanges');
    this.$store.dispatch('balance/listenToContractBalanceChanges');
    this.$store.dispatch('blockchain/listenToGasPriceChanges');
  },

  beforeDestroy() {
    this.$store.dispatch('blockchain/stopListeningToGasPriceChanges');
    this.$store.dispatch('deposit/stopListeningToBalanceOperations');
    this.$store.dispatch('balance/stopListeningToPrivateBalanceChanges');
    this.$store.dispatch('balance/stopListeningToContractBalanceChanges');
  },

  validations: {
    deposit: {
      required,
      decimal,
      greaterThanZero,
      maxDecimalPlaces(value) {
        return maxDecimalPlaces(this.currency.decimals)(value);
      },
    },
  },

  icons: {
    iconWallet,
    iconMetamask,
    iconLedger,
  },

  methods: {
    setDepositMax() {
      const maxDeposit = this.isEth
        ? BigNumber.max(this.privateBalance.minus(this.fee), ZERO)
        : this.privateBalance;
      this.deposit = maxDeposit.shiftedBy(-this.currency.decimals).toFixed();
    },

    submit() {
      // TODO much cooler validation
      if (!this.$v.$invalid) {
        const value = BigNumber(this.deposit).shiftedBy(this.currency.decimals).toFixed();
        if (this.isEth) {
          this.$store.dispatch('deposit/submitDepositEthWithoutPopup', { value });
          this.$emit('deposit');
        } else {
          const tokenAddress = this.currencyAddress;
          this.$store.dispatch('deposit/submitDepositTokenWithoutPopup', { tokenAddress, value });
          this.$emit('deposit', {
            tokenAddress: this.currencyAddress,
          });
        }
      }
    },
  },

  components: {
    EInputNumber,
    ESelectToken,
    EBigNumber,
    EButton,
    EIcon,
  },

  mixins: [validationMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wizard-deposit-connected {
  $this: &;

  padding: 0 0 34px;

  &__wallet {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    letter-spacing: -.5px;
  }

  &__wallet-address {
    color: #00E997;
    padding-right: 6px;
  }

  &__via-metamask {
    color: #F4852D;
    text-transform: uppercase;
    display: inline-flex;
    margin-left: 4px;
  }

  &__via-metamask-icon {
    width: 21px;
    height: 19px;
    margin-left: 4px;
  }

  &__via-ledger {
    color: #BBC1DD;
    padding: 0 6px;
    text-transform: uppercase;
    display: inline-flex;
  }

  &__via-ledger-icon {
    fill: #BCC1DB;
    margin: 2px 0 0 6px;
    width: 22px;
    height: 22px;
  }

  &__select-wrapper {
    padding-top: 25px;
    width: 500px;
    margin: 0 auto;
    transform: translate(-35px, 0);
    position: relative;
    z-index: 10;

    .select-token__select-result-token-icon {
      flex: 0 0 56px;
    }

    .select-token__select-result-token-icon .e-tocken-icon {
      transform: translate(2px, 0);
    }
  }

  &__wallets-wrapper {
    display: flex;
    justify-content: center;
    margin: 30px 0 0;
  }

  &__wallet-item {
    display: flex;
    width: 190px;
    transform: translate(-3px, 0);
  }

  &__wallet-icon {
    width: 29px;
    height: 25px;
    margin-right: 16px;
  }

  &__wallet-icon-metamask {
    width: 18px;
    height: 17px;
  }

  &__wallet-icon-ledger {
    fill: #fff;
    width: 22px;
    height: 22px;
  }

  &__wallet-icon-contract {
    .e-icon__wallet-colored {
      fill: #275379;
    }
  }

  &__wallet-value {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 16px;
    font-weight: 500;
    color: #fff;

    .e-big-number__main-lasts {
      opacity: .4;
    }
  }

  &__wallet-value-currency {
    color: #fff;
    opacity: .4;
    padding-left: 3px;
  }

  &__wallet-label {
    font-size: 13px;
    line-height: 20px;
    font-weight: 500;
    color: #fff;
  }

  &__deposit {
    display: flex;
    align-items: center;
    padding: 28px 0 10px 26px;
    width: 542px;
    margin: 0 auto;
    transform: translate(-79px, 0);
  }

  &__deposit-label {
    font-size: 13px;
    font-weight: 500;
    line-height: 17px;
    color: #fff;
    flex: 0 0 90px;
    margin-right: 20px;
    text-align: right;
  }

  &__deposit-label-all {
    text-transform: uppercase;
    color: #00E997;
    cursor: pointer;
  }

  &__deposit-input {
    .e-input-number_wide {
      .e-input-number__box {
        width: 215px;
        height: 57px;
        border-radius: 5px;
      }

      .e-input-number__unit {
        top: 28px;
        right: 18px;
        font-size: 16px;
        font-weight: 500;
      }

      .e-input-number__box::before {
        border-radius: 5px;
      }

      .e-input-number__input {
        padding-left: 20px;
        font-size: 16px;
        font-weight: 500;
        letter-spacing: -.5px;
        color: #333;
        border-radius: 5px;

        &.e-input-number__input_padd-right-sm {
          font-size: 14px !important;
        }

        &::placeholder {
          font-style: normal;
        }
      }
    }
  }

  &__deposit-fee {
    position: absolute;
    transform: translate(0, 15px);
    display: flex;
    font-size: 12px;
    font-weight: 500;
    color: rgba(129, 168, 247, .5);
  }

  &__deposit-fee-amount {
    color: #fff;
    margin-left: 3px;
  }

  &__deposit-submit {
    margin-left: 20px;

    .e-button {
      width: 196px;
    }
  }
}
</style>



