<template>
  <div
    class="the-wizard-wallet-types"
    :class="{ 'with-connecting' : processedWalletOption }"
  >
    <div
      class="the-wizard-wallet-types__item"
      :class="[
        isCreatingWallet ? 'is-connecting' : 'is-active',
      ]"
      @click="createWallet"
    >
      <div class="the-wizard-wallet-types__item-wrapper">
        <div class="the-wizard-wallet-types__header">
          <div class="the-wizard-wallet-types__header-text">Create <br /> a new wallet</div>
          <div class="the-wizard-wallet-types__header-icon the-wizard-wallet-types__icon-wallet-create">
            <e-icon :src="iconWalletCreate" />
          </div>
        </div>
        <div class="the-wizard-wallet-types__content">Generate a brand new wallet <br /> in 1 click.</div>
        <div class="the-wizard-wallet-types__button">
          <e-button
            fill="transparent-white"
            size="s"
            fontWeight="500"
            textTransform="none"
            fontSize="18"
            width="full"
          >
            Create Wallet
          </e-button>
        </div>
      </div>

      <div
        class="the-wizard-wallet-types__item-wrapper-connecting"
      >
        <div class="the-wizard-wallet-types__header-connecting">
          <div class="the-wizard-wallet-types__header-connecting-icon the-wizard-wallet-types__connecting-icon-create">
            <e-icon :src="iconWalletCreate" />
          </div>
          <div class="the-wizard-wallet-types__connecting-headline">
            Creating a new wallet
          </div>
        </div>
        <div class="the-wizard-wallet-types__connecting-content">
          Generating credentials, public and private keys...
        </div>
        <div class="the-wizard-wallet-types__connecting-button">
          <div class="the-wizard-wallet-types__connecting-refresh">
            <e-icon :src="iconRefresh" />
          </div>
        </div>
      </div>
    </div>

    <div
      class="the-wizard-wallet-types__item"
      @click="importWallet"
    >
      <div class="the-wizard-wallet-types__item-wrapper">
        <div class="the-wizard-wallet-types__header">
          <div class="the-wizard-wallet-types__header-text">Wallet Address <br />& Private Key</div>
          <div class="the-wizard-wallet-types__header-icon the-wizard-wallet-types__icon-key">
            <e-icon :src="iconKey" />
          </div>
        </div>
        <div class="the-wizard-wallet-types__content">Connect an ETH wallet using your wallet address and private key</div>
        <div class="the-wizard-wallet-types__button">
          <e-button
            fill="transparent-white"
            size="s"
            fontWeight="500"
            textTransform="none"
            fontSize="18"
            width="full"
          >
            Import Wallet
          </e-button>
        </div>
      </div>
    </div>

    <div
      class="the-wizard-wallet-types__item"
      :class="{
        'is-connecting' : metaMaskConnecting,
        'is-failed': metaMaskError && !metaMaskConnecting,
      }"
      @click="connectMetaMask"
    >
      <div class="the-wizard-wallet-types__item-wrapper">
        <div class="the-wizard-wallet-types__header">
          <div class="the-wizard-wallet-types__header-text">MetaMask <br/>Plugin</div>
          <div class="the-wizard-wallet-types__header-icon the-wizard-wallet-types__icon-metamask">
            <e-icon :src="iconMetamask" />
          </div>
        </div>

        <div class="the-wizard-wallet-types__content">
          {{ metaMaskError || 'Unlock your MetaMask Chrome Plugin and trade straight from your MetaMask wallet' }}
        </div>

        <div class="the-wizard-wallet-types__button">
          <e-button
            fill="transparent-white"
            size="s"
            fontWeight="500"
            textTransform="none"
            fontSize="18"
            width="full"
          >
            Connect Metamask
          </e-button>
        </div>
      </div>

      <div class="the-wizard-wallet-types__item-wrapper-connecting">
        <div class="the-wizard-wallet-types__header-connecting">
          <div class="the-wizard-wallet-types__header-connecting-icon the-wizard-wallet-types__connecting-icon-metamask">
            <e-icon :src="iconMetamask" />
          </div>
          <div class="the-wizard-wallet-types__connecting-headline">
            Connecting to MetaMask
          </div>
        </div>
        <div class="the-wizard-wallet-types__connecting-content">
          Checking your browser to see if your <a href="#">MetaMask widget</a> is connected...
        </div>
        <div class="the-wizard-wallet-types__connecting-button">
          <div
            class="the-wizard-wallet-types__connecting-refresh"
            v-if="metaMaskConnecting"
          >
            <e-icon :src="iconRefresh" />
          </div>

          <e-button
            fill="transparent-white"
            size="s"
            fontWeight="500"
            textTransform="none"
            fontSize="18"
            width="full"
            v-if="!metaMaskConnecting"
          >
            Connect Metamask
          </e-button>
        </div>
      </div>
    </div>

    <div
      class="the-wizard-wallet-types__item"
      :class="{
        'is-connecting' : ledgerConnecting,
        'is-failed': ledgerError,
      }"
      @click="connectLedger"
    >
      <div class="the-wizard-wallet-types__item-wrapper">
        <div class="the-wizard-wallet-types__header">
          <div class="the-wizard-wallet-types__header-text">Ledger <br />Nano S</div>
          <div class="the-wizard-wallet-types__header-icon the-wizard-wallet-types__icon-ledger">
            <e-icon :src="iconLedger" />
          </div>
        </div>
        <div class="the-wizard-wallet-types__content">
          {{ ledgerError || 'From 1st of October we support Ledger Nano S wallets' }}
        </div>
        <div class="the-wizard-wallet-types__button">
          <e-button
            fill="transparent-white"
            size="s"
            fontWeight="500"
            textTransform="none"
            fontSize="18"
            width="full"
          >
            Connect Ledger
          </e-button>
        </div>
      </div>

      <div class="the-wizard-wallet-types__item-wrapper-connecting">
        <div class="the-wizard-wallet-types__header-connecting">
          <div class="the-wizard-wallet-types__header-connecting-icon the-wizard-wallet-types__connecting-icon-ledger">
            <e-icon :src="iconLedger" />
          </div>
          <div class="the-wizard-wallet-types__connecting-headline">
            Connecting to Ledger Nano S
          </div>
        </div>
        <div class="the-wizard-wallet-types__connecting-content">
          Checking connection to<br>
          Ledger Nano S
        </div>
        <div class="the-wizard-wallet-types__connecting-button">
          <div class="the-wizard-wallet-types__connecting-refresh">
            <e-icon :src="iconRefresh" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Accounts } from '@/api/ethereum';
import delay from 'delay';
import { WALLET_CREATION_TIME } from '@/config';
import iconWalletCreate from '@/icons/e-icon__wallet-create.svg';
import iconKey from '@/icons/e-icon__key.svg';
import iconMetamask from '@/icons/e-icon_metamask-normal.svg';
import iconLedger from '@/icons/e-icon__ledger.svg';
import iconRefresh from '@/icons/e-icon__refresh.svg';

import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';

import { CREATE, IMPORT, METAMASK, LEDGER } from './TheWizard';

export default {
  name: 'the-wizard-wallet-types',

  data() {
    return {
      metaMaskError: null,
      ledgerError: null,
      processedWalletOption: null,
    };
  },

  computed: {
    isCreatingWallet() {
      return this.processedWalletOption === CREATE;
    },

    metaMaskConnecting() {
      return this.processedWalletOption === METAMASK;
    },

    ledgerConnecting() {
      return this.processedWalletOption === LEDGER;
    },
  },

  created() {
    Object.assign(this, {
      iconWalletCreate,
      iconKey,
      iconMetamask,
      iconLedger,
      iconRefresh,
    });
  },

  mounted() {
    this.$store.dispatch('wallets/unloadMetamaskWallets');
  },

  destroyed() {
    this.destroyed = true;
  },

  methods: {
    async createWallet() {
      if (this.processedWalletOption) return;
      this.processedWalletOption = CREATE;
      await delay(WALLET_CREATION_TIME);
      if (this.destroyed) return; // cancel any activity if component is destroyed

      this.$store.dispatch('wallets/createWallet');
      this.$emit(CREATE);
    },

    importWallet() {
      this.processedWalletOption = IMPORT;
      this.$emit(IMPORT);
    },

    // TODO check wallet change
    async connectMetaMask() {
      if (this.processedWalletOption) return;
      this.processedWalletOption = METAMASK;
      let error;
      const minAnimationDelay = delay(WALLET_CREATION_TIME);

      if (!Accounts.isEthereumCompatibleBrowser) {
        error = 'MetaMask is not installed. Download MetaMask plugin from Google Store.';
      }

      try {
        await Accounts.getBrowserProviderPermission();
      } catch (e) {
        if (e.message === 'Timeout occured') {
          error = 'There is no access to your MetaMask. Please log in and provide access';
        } else {
          error = 'MetaMask Privacy Mode is enabled for EtherMium.com. Please provide EtherMium.com the permission to see your MetaMask account.';
        }
      }
      if (this.destroyed) return; // cancel any activity if component is destroyed

      if (!error) {
        await this.$store.dispatch('wallets/loadMetamaskWallets');
        if (!this.$store.state.wallets.providedWallets.length) {
          error = 'MetaMask refused to show your account. Please check if MetaMask works properly and try again if MetaMask is fine.';
        }
        if (this.destroyed) return; // cancel any activity if component is destroyed
      }

      await minAnimationDelay;
      if (this.destroyed) return; // cancel any activity if component is destroyed

      if (error) {
        this.onMetaMaskFail(error);
      } else {
        this.$store.dispatch('wallets/setActiveWallet', this.$store.state.wallets.providedWallets[0]);
        this.$emit(METAMASK);
      }
    },

    onMetaMaskFail(error) {
      this.metaMaskError = error;
      this.reset();
      this.$store.dispatch('wallets/unloadMetamaskWallets');
    },

    async connectLedger() {
      if (this.processedWalletOption) return;
      let error;
      this.processedWalletOption = LEDGER;
      const minAnimationDelay = delay(WALLET_CREATION_TIME);

      let accounts;
      try {
        accounts = await this.getLedgerAccounts();
      } catch (e) {
        error = e.message;
      }
      if (this.destroyed) return; // cancel any activity if component is destroyed

      await minAnimationDelay;
      if (this.destroyed) return; // cancel any activity if component is destroyed

      if (error) {
        this.onLedgerFail(error);
      } else {
        this.$store.commit('wallets/SET_LEDGER_WALLETS', accounts);
        this.$store.dispatch('wallets/setActiveWallet', accounts[0]);
        this.$emit(LEDGER);
      }
    },

    async getLedgerAccounts() {
      const supported = await Accounts.isLedgerSupported();
      if (this.destroyed) throw new Error('Component destroyed'); // cancel any activity if component is destroyed
      if (!supported) {
        throw new Error('Ledger Nano S. is not supported in your browser');
      }
      const accounts = await Accounts.getLedgerAccounts();
      if (this.destroyed) throw new Error('Component destroyed'); // cancel any activity if component is destroyed
      if (!accounts.length) {
        throw new Error("We couldn't get your Ledger Nano S account. Please, check if Ledger is connected, unlocked and has Ethereum app active.");
      }
      return accounts;
    },

    onLedgerFail(error) {
      this.ledgerError = error;
      this.reset();
    },

    reset() {
      this.processedWalletOption = null;
    },
  },

  components: {
    EIcon,
    EButton,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wizard-wallet-types {
  $this: &;
  display: flex;
  flex-wrap: wrap;
  margin: 0 -10px;
  position: relative;

  &.with-connecting {
    &:before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      display: block;
      z-index: 1;
    }

    #{$this}__item {
      opacity: .1;

      &.is-connecting {
        opacity: 1;
        position: relative;
        z-index: 2;
      }
    }
  }

  &__item-wrapper {
    display: flex;
    flex-direction: column;
    flex: 0 0 100%;
    height: 100%;
    padding: 30px 30px 22px;
    box-sizing: border-box;
    position: relative;
    z-index: 10;
    transition: transform .25s ease-in-out;
  }

  &__item-wrapper-connecting {
    display: flex;
    flex-direction: column;
    flex: 0 0 100%;
    height: 100%;
    padding: 30px 30px 22px;
    box-sizing: border-box;
    position: relative;
    z-index: 10;
    transform: translate(0, 0);
    transition: transform .35s ease-in-out;
  }

  &__item {
    display: flex;
    flex: 1 1 0;
    margin: 0 10px;
    border-radius: 3px;
    background: #001593;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    transition: opacity .35s ease-in-out;

    &::before,
    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      // transition: opacity .15s ease-in-out;
      border-radius: 3px;
    }

    &::before {
      background-image: linear-gradient(-135deg, #001593 0%, #0018A8 100%);
    }

    &::after {
      background-image: linear-gradient(to bottom, #2630cf 0%, #2630cf 40%, rgba(3, 101, 215, 1) 100%);;
      opacity: 0;
    }

    &:hover {
      &:before {
        opacity: 0;
      }

      &::after {
        opacity: 1;
      }

      #{$this}__content {
        color: #fff;
        opacity: 1;
      }
    }

    &.is-active {
      &::after {
        background-image: linear-gradient(-180deg, #69FFE3 0%, #0CFFB3 100%);
        opacity: 1;
      }

      #{$this}__header-text {
        color: #030F5F;
      }

      #{$this}__content {
        color: #030F5F;
      }

      .e-button {
        color: #030F5F;
        border-color: rgba(31, 46, 233, .2);

        &:hover {
          border-color: transparent;
        }
      }
    }

    &.is-connecting {
      opacity: 1;

      #{$this}__item-wrapper {
        transform: translate(-100%, 0);
      }

      #{$this}__item-wrapper-connecting {
        transform: translate(-100%, 0);
      }

      &:before {
        opacity: 0;
      }

      &::after {
        opacity: 1;
      }

      #{$this}__content {
        color: #fff;
        opacity: 1;
      }
    }

    &.is-failed {
      opacity: 1;
      line-height: (19 / 13);

      #{$this}__content {
        font-size: 16px;
        opacity: .6;
        color: #fff;
      }

      &:before {
        opacity: 1;
        background: linear-gradient(135deg, rgba(254, 35, 35, 1) 0%, rgba(168, 0, 0, 1) 100%);
      }

      &::after {
        opacity: 0;
      }
    }

    @include r(1150) {
      flex: 0 0 calc(50% - 20px);
      margin-bottom: 20px;
    }

    @include r(600) {
      flex: 0 0 calc(100% - 20px);
    }
  }

  &__header-connecting-icon {
    position: relative;
    height: 50px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__connecting-icon-create .e-icon {
    width: 36px;
    height: 29px;
  }

  &__connecting-icon-metamask .e-icon {
    width: 36px;
    height: 33px;
  }

  &__connecting-icon-ledger .e-icon {
    width: 33px;
    height: 29px;
  }

  &__connecting-headline {
    color: #fff;
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    text-align: center;
    padding-bottom: 8px;
  }

  &__connecting-content {
    font-size: 16px;
    line-height: 19px;
    color: rgba(255, 255, 255, .6);
    font-weight: 500;
    text-align: center;
    padding-bottom: 12px;

    a {
      color: rgba(255, 255, 255, .6);
      transition: color .15s ease-in-out;

      &:hover {
        color: #fff;
      }
    }
  }

  &__connecting-button {
    display: flex;
    justify-content: center;
    margin-top: auto;
  }

  &__connecting-refresh {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    border-radius: 50%;

    @keyframes rotating {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }

    .e-icon {
      width: 20px;
      height: 15px;
      animation: rotating 1.8s linear infinite;
    }
  }

  &__header {
    display: flex;
    align-items: center;
    padding-bottom: 25px;
    position: relative;
    z-index: 1;
  }

  &__header-text {
    flex: 1;
    margin-right: 30px;
    font-size: 23px;
    font-weight: 500;
    line-height: 25px;
    color: #fff;
    transition: color .15s ease-in-out;
  }

  &__header-icon {
    margin-right: 12px;
  }

  &__icon-wallet-create {
    width: 36px;
    height: 29px;
  }

  &__icon-key {
    width: 33px;
    height: 33px;
  }

  &__icon-metamask {
    width: 36px;
    height: 33px;
  }

  &__icon-ledger {
    width: 33px;
    height: 33px;
  }

  &__content {
    font-size: 16px;
    font-weight: 500;
    color: #81A8F7;
    opacity: .6;
    letter-spacing: 0;
    box-sizing: border-box;
    padding-right: 12px;
    padding-bottom: 20px;
    transition: color .15s ease-in-out, opacity .15s ease-in-out;
    position: relative;
    z-index: 1;
  }

  &__button {
    margin-top: auto;
    position: relative;
    z-index: 1;

    .e-button {
      padding-bottom: 3px;
    }
  }
}
</style>



