<template>
  <div class="the-wizard-new-deposit">
    <div class="the-wizard-new-deposit__select-wrapper">
      <e-select-token
        :currency-address="currencyAddress"
        @update:currencyAddress="setCurrencyAddress"
      />
    </div>

    <div class="the-wizard-new-deposit__wallets-wrapper">
      <div class="the-wizard-new-deposit__wallet-item">
        <div class="the-wizard-new-deposit__wallet-icon">
          <e-icon :src="$options.icons.iconWallet" />
        </div>

        <div class="the-wizard-new-deposit__wallet-amount">
          <div class="the-wizard-new-deposit__wallet-value">
            <e-big-number
              :value="privateBalance"
              :decimals="currency.decimals"
              :min-decimal-places="DEFAULT_MIN_DECIMALS"
              :max-decimal-places="DEFAULT_MAX_DECIMALS"
            />
            <div class="the-wizard-new-deposit__wallet-value-currency">
              ETH
            </div>
          </div>
          <div class="the-wizard-new-deposit__wallet-label">Private Balance</div>
        </div>
      </div>

      <div class="the-wizard-new-deposit__wallet-item">
        <div class="the-wizard-new-deposit__wallet-icon the-wizard-new-deposit__wallet-icon-contract">
          <e-icon :src="$options.icons.iconWallet" />
        </div>
        <div class="the-wizard-new-deposit__wallet-amount">
          <div class="the-wizard-new-deposit__wallet-value">
            <e-big-number
              :value="contractBalance"
              :decimals="currency.decimals"
              :min-decimal-places="DEFAULT_MIN_DECIMALS"
              :max-decimal-places="DEFAULT_MAX_DECIMALS"
            />
            <div class="the-wizard-new-deposit__wallet-value-currency">
              ETH
            </div>
          </div>
          <div class="the-wizard-new-deposit__wallet-label">Contract Balance</div>
        </div>
      </div>
    </div>

    <div class="the-wizard-new-deposit__send-wrapper">
      <div class="the-wizard-new-deposit__send-label">
        Send
        <template v-if="isEthSelected">
          <span class="the-wizard-new-deposit__send-label-green">ETH</span>
        </template>
        <template v-else>
          your <span class="the-wizard-new-deposit__send-label-blue">{{ currency.shortName }}</span> and <span class="the-wizard-new-deposit__send-label-green">min. 0.01 ETH</span>
        </template>
        to this address:
      </div>

      <div class="the-wizard-new-deposit__send-wrapper-wallet">
        <e-copy-text
          title="Wallet address"
          :hash="walletAddress"
          compact
          qr-code-inline
          btn-name="Copy"
          inline
          colorScheme="blue"
          size="s"
        />
      </div>
    </div>

    <div class="the-wizard-new-deposit__status-wrapper">
      <div class="the-wizard-new-deposit__status-label">
        Status
      </div>

      <template v-if="isEthSelected">
        <div class="the-wizard-new-deposit__status-send">
          <div
            v-if="isAbleToDepositEth"
            class="the-wizard-new-deposit__status-send-ok"
          >
            <e-icon :src="$options.icons.iconFinished" />
          </div>
          <div
            v-else
            class="the-wizard-new-deposit__status-send-refresh"
          >
            <e-icon :src="$options.icons.iconRefresh" />
          </div>
          <div class="the-wizard-new-deposit__status-send-content">
            <div class="the-wizard-new-deposit__status-send-description">
              Send ETH
            </div>
          </div>
        </div>
      </template>

      <template v-else>
        <div class="the-wizard-new-deposit__status-send">
          <div
            v-if="isAbleToPayTokenFee"
            class="the-wizard-new-deposit__status-send-ok"
          >
            <e-icon :src="$options.icons.iconFinished" />
          </div>
          <div
            v-else
            class="the-wizard-new-deposit__status-send-refresh"
          >
            <e-icon :src="$options.icons.iconRefresh" />
          </div>
          <div class="the-wizard-new-deposit__status-send-content">
            <div class="the-wizard-new-deposit__status-send-description">
              Send min. 0.01 ETH

              <div class="the-wizard-new-deposit__hint">
                ?
                <div class="the-wizard-new-deposit__tooltip">
                  This is needed to cover transaction costs that can be only paid in ETH.
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="the-wizard-new-deposit__status-send">
          <div
            v-if="!privateBalance.isZero()"
            class="the-wizard-new-deposit__status-send-ok"
          >
            <e-icon :src="$options.icons.iconFinished" />
          </div>
          <div
            v-else
            class="the-wizard-new-deposit__status-send-refresh"
          >
            <e-icon :src="$options.icons.iconRefresh" />
          </div>
          <div class="the-wizard-new-deposit__status-send-content">
            <div class="the-wizard-new-deposit__status-send-description">
              Send {{ currency.shortName }} tokens
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { ETH_ADDRESS } from '@/util/constants';
import { mapGetters } from 'vuex';
import iconWallet from '@/icons/e-icon__wallet2.svg';
import iconRefresh from '@/icons/e-icon__refresh.svg';
import iconFinished from '@/icons/e-icon__checkmark-outline.svg';

import EBigNumber from '@/components/EBigNumber';
import ESelectToken from '@/components/ESelectToken';
import ECopyText from '@/components/ECopyText';
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';

const DELAY_BEFORE_AUTO_DEPOSIT = 3 * 1000;

/**
 * Precondition: Private Key wallet is active.
 * @property autoDepositWatcher
 * @property isBalanceConfirmedEnoughTimeTimer
 */
export default {
  name: 'the-wizard-new-deposit',

  data() {
    return {
      currencyAddress: ETH_ADDRESS,
      isBalanceConfirmedEnoughTime: false,
    };
  },

  computed: {
    ...mapGetters({
      walletAddress: 'wallets/activeWalletAddress',
      isAbleToDepositEth: 'deposit/isAbleToDepositEth',
      isAbleToPayTokenFee: 'deposit/isAbleToPayTokenFee',
    }),

    isEthSelected() {
      return this.currencyAddress === ETH_ADDRESS;
    },

    currency() {
      return this.$store.getters['tokens/tokenByContract'](this.currencyAddress);
    },

    isEthDepositPossible() {
      return (
        this.isBalanceConfirmedEnoughTime &&
        this.isAbleToDepositEth
      );
    },

    isSelectedTokenDepositPossible() {
      return (
        !this.isEthSelected &&
        this.isBalanceConfirmedEnoughTime &&
        this.isAbleToPayTokenFee &&
        !this.privateBalance.isZero()
      );
    },

    depositBalanceReady() {
      return this.isEthSelected
        ? this.isAbleToDepositEth
        : this.isAbleToPayTokenFee && !this.privateBalance.isZero();
    },

    privateBalance() {
      if (this.isEthSelected) {
        return this.$store.getters['balance/privateEthAvailable'];
      }
      return this.$store.getters['balance/getPrivateBalanceFor'](this.currencyAddress);
    },

    contractBalance() {
      if (this.isEthSelected) {
        return this.$store.getters['balance/contractEthAvailable'];
      }
      return this.$store.getters['balance/getContractBalanceFor'](this.currencyAddress);
    },
  },

  watch: {
    depositBalanceReady: 'updateIsBalanceConfirmedEnoughTime',
  },

  created() {
    this.tryToAutoDepositEth = this.tryToAutoDepositEth.bind(this);
  },

  mounted() {
    this.$store.dispatch('deposit/listenToBalanceOperations');
    this.$store.dispatch('balance/listenToPrivateBalanceChanges');
    this.$store.dispatch('balance/listenToContractBalanceChanges');
    this.$store.dispatch('blockchain/listenToGasPriceChanges');
    this.autoDepositWatcher = this.$watch('isEthDepositPossible', this.tryToAutoDepositEth, { immediate: true });
  },

  beforeDestroy() {
    this.$store.dispatch('blockchain/stopListeningToGasPriceChanges');
    this.$store.dispatch('deposit/stopListeningToBalanceOperations');
    this.$store.dispatch('balance/stopListeningToPrivateBalanceChanges');
    this.$store.dispatch('balance/stopListeningToContractBalanceChanges');
  },

  methods: {
    async tryToAutoDepositEth() {
      if (this.isEthDepositPossible) {
        this.$store.dispatch('deposit/submitDepositAllEthWithoutPopup');
        this.$emit('deposit');
      }
    },

    async tryToAutoDepositToken() {
      if (this.isSelectedTokenDepositPossible) {
        this.$store.dispatch('deposit/submitDepositAllTokenWithoutPopup', { tokenAddress: this.currencyAddress });
        this.$emit('deposit', {
          tokenAddress: this.currencyAddress,
        });
      }
    },

    setCurrencyAddress(currencyAddress) {
      this.currencyAddress = currencyAddress;

      // timer for confirm ETH and token balance
      this.updateIsBalanceConfirmedEnoughTime();

      // update watcher
      this.autoDepositWatcher();
      if (currencyAddress === ETH_ADDRESS) {
        this.autoDepositWatcher = this.$watch('isEthDepositPossible', this.tryToAutoDepositEth, { immediate: true });
      } else {
        this.autoDepositWatcher = this.$watch('isSelectedTokenDepositPossible', this.tryToAutoDepositToken, { immediate: true });
      }
    },

    updateIsBalanceConfirmedEnoughTime() {
      if (this.depositBalanceReady) {
        this.isBalanceConfirmedEnoughTime = false;
        clearTimeout(this.isBalanceConfirmedEnoughTimeTimer);
        this.isBalanceConfirmedEnoughTimeTimer = setTimeout(() => {
          this.isBalanceConfirmedEnoughTime = true;
        }, DELAY_BEFORE_AUTO_DEPOSIT);
      }
    },
  },

  icons: {
    iconWallet,
    iconRefresh,
    iconFinished,
  },

  components: {
    EBigNumber,
    ESelectToken,
    ECopyText,
    EButton,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wizard-new-deposit {
  $this: &;

  &__select-wrapper {
    padding-top: 15px;
    width: 500px;
    margin: 0 auto;
    transform: translate(-35px, 0);
    position: relative;
    z-index: 10;

    .select-token__select-result-token-icon {
      flex: 0 0 56px;
    }

    .select-token__select-result-token-icon .e-tocken-icon {
      transform: translate(2px, 0);
    }
  }

  &__wallets-wrapper {
    display: flex;
    justify-content: center;
    margin: 30px 0 0;
  }

  &__wallet-item {
    display: flex;
    width: 198px;
  }

  &__wallet-icon {
    width: 29px;
    height: 25px;
    margin-right: 12px;
  }

  &__wallet-icon-contract {
    .e-icon__wallet-colored {
      fill: #275379;
    }
  }

  &__wallet-value {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 16px;
    font-weight: 500;
    color: #fff;

    .e-big-number__main-lasts {
      opacity: .4;
    }
  }

  &__wallet-value-currency {
    color: #fff;
    opacity: .4;
    padding-left: 3px;
  }

  &__wallet-label {
    font-size: 13px;
    line-height: 20px;
    font-weight: 500;
    color: #fff;
  }

  &__send-wrapper {
    padding-top: 28px;
  }

  &__send-label {
    font-size: 16px;
    font-weight: 500;
    letter-spacing: -.75px;
    color: #fff;
    padding-left: 135px;
    padding-bottom: 6px;
    box-sizing: border-box;
  }

  &__send-label-blue,
  &__send-label-green {
    padding: 2px 4px 3px;
    border-radius: 4px;
    margin: 0 3px;
    font-size: 16px;
  }

  &__send-label-blue {
    background: #1F2EE9;
    color: #fff;
  }

  &__send-label-green {
    background: #00E997;
    color: #0D105B;
  }

  &__send-wrapper-wallet {
    width: 500px;
    padding-top: 4px;
    margin: 0 auto;
    transform: translate(-75px, 0);

    .e-copy-text__wrapper {
      margin-right: 18px;
    }

    .e-copy-text__head {
      flex: 0 0 100px;
      margin-right: 8px;
    }

    .e-copy-text__qr-code-inline {
      top: -38px;
      right: -66px;
    }

  }

  &__status-wrapper {
    display: flex;
    padding-top: 15px;
    padding-left: 14px;
  }

  &__status-label {
    flex: 0 0 100px;
    margin-right: 20px;
    text-align: right;
    color: #fff;
    font-size: 13px;
    font-weight: 500;
  }

  &__status-send {
    display: flex;
    margin-right: 30px;
  }

  &__status-send-ok {
    width: 18px;
    height: 18px;
    margin-right: 10px;
    margin-top: 2px;
  }

  &__status-send-refresh {
    width: 20px;
    height: 15px;
    margin-right: 10px;
    margin-top: 2px;
    animation: rotating 1.8s linear infinite;

    svg path {
      fill: #00E997;
    }
  }

  &__status-send-description {
    height: 22px;
    font-size: 16px;
    letter-spacing: -.5px;
    font-weight: 500;
    line-height: 18px;
    color: #fff;
  }


  /* Tooltip */
  &__hint {
    min-width: 20px;
    min-height: 20px;
    max-width: 20px;
    max-height: 20px;
    border-radius: 50%;
    border: 1px solid #2CBE8B;
    background-color: #2CBE8B;
    color: #0D105B;
    font-weight: 500;
    font-size: 12px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 3;
    cursor: pointer;
    margin-left: 10px;

    &:hover {
      color: #fff;
      #{$this}__tooltip {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  &__tooltip {
    font-size: 12px;
    line-height: 15px;
    font-weight: 500;
    position: absolute;
    right: 10px;
    bottom: 30px;
    background-color: #2CBE8B;
    color: #fff;
    padding: 8px 15px;
    box-sizing: border-box;
    visibility: hidden;
    opacity: 0;
    transition: $transition-time;
    min-width: 200px;
    width: 100%;
    border-radius: 5px 5px 0 5px;

    &:before {
      content: '';
      position: absolute;
      bottom: -6px;
      right: 0;
      width: 0;
      height: 0;
      border-top: 6px solid #2CBE8B;
      border-left: 9px solid transparent;
    }
  }


  @keyframes rotating {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
}
</style>



