<template>
  <div class="the-wizard-import">
    <div class="the-wizard-import__switch">
      <div
        class="the-wizard-import__switch-item"
        :class="{
          'is-selected': selectedSourceType === 'privateKey'
        }"
        @click="selectSourceType('privateKey')"
      >
        Private key
      </div>
      <div
        class="the-wizard-import__switch-item"
        :class="{'is-selected': selectedSourceType === 'keystore'}"
        @click="selectSourceType('keystore')"
      >
        Keystore
      </div>
      <div
        class="the-wizard-import__switch-item"
        :class="{'is-selected': selectedSourceType === 'mnemonicPhrase'}"
        @click="selectSourceType('mnemonicPhrase')"
      >
        Mnemonic phrase
      </div>
    </div>

    <div
      v-if="selectedSourceType === 'privateKey'"
      class="the-wizard-import__content"
    >
      <div class="the-wizard-import__content-description">
        Your private key will be stored on your browser. EtherMium never sees your private keys.
      </div>

      <div class="the-wizard-import__input-wrapper">
        <div class="the-wizard-import__input-label">Private Key</div>
        <e-text-input
          v-model="privateKey"
          placeholder="Insert your Private Key"
          class="e-text-input-validated"
        />
      </div>

      <div class="the-wizard-import__unlock">
        <div class="the-wizard-import__unlock-description">
          We dont store your private key on our servers. Remember  were decentralized.
        </div>
        <div class="the-wizard-import__unlock-button-wrapper">
          <e-button
            :disabled="$v.privateKey.$invalid"
            fill="green-white"
            textTransform="none"
            fontWeight="500"
            fontSize="16"
            class="the-wizard-import__unlock-button"
            @click="importPrivateKey"
          >
            Unlock Wallet
          </e-button>
        </div>
      </div>
    </div>

    <div
      v-if="selectedSourceType === 'keystore'"
      class="the-wizard-import__content"
    >
      <div class="the-wizard-import__upload">
        <e-upload-files
          accept="*.*"
          acceptLabel="*.*"
          label="Open Keystore file..."
          small-design
          class="the-wallet-import-keystore__file-upload"
          @change="onKeystoreFileUpload"
          fill="transparent-white"
          display="square"
        />
        <div class="the-wizard-import__upload-label">.TXT FILE</div>
      </div>
      <div class="the-wizard-import__keystore-password">
        <div class="the-wizard-import__keystore-label">File Password</div>
        <div class="the-wizard-import__keystore-input">
          <e-text-input
            v-model="keystore.password"
            type="password"
          />
        </div>
      </div>


      <div class="the-wizard-import__unlock">
        <div class="the-wizard-import__unlock-description-keystore">
          We dont store your private key on our servers. Remember  were decentralized.
        </div>
        <div class="the-wizard-import__unlock-button-wrapper">
          <e-button
            :disabled="$v.keystore.$invalid"
            fill="green-white"
            textTransform="none"
            fontWeight="500"
            fontSize="16"
            class="the-wizard-import__unlock-button"
            @click="importKeystore"
          >
            Unlock Wallet
          </e-button>
        </div>
      </div>
    </div>

    <div
      v-if="selectedSourceType === 'mnemonicPhrase'"
      class="the-wizard-import__content"
    >
      <div class="the-wizard-import__mnemonicPhrase-input">
        <div class="the-wizard-import__mnemonicPhrase-label">
          Mnemonic <br /> Recovery <br />Phrase
        </div>
        <e-text-area
          v-model="mnemonicPhrase"
        />
        <div class="the-wizard-import__mnemonicPhrase-description">
          12, 18,
          <br />
          or 24-words
        </div>
      </div>


      <div class="the-wizard-import__unlock">
        <div class="the-wizard-import__unlock-description-keystore">
          We dont store your private key on our servers. Remember  were decentralized.
        </div>
        <div class="the-wizard-import__unlock-button-wrapper">
          <e-button
            :disabled="$v.mnemonicPhrase.$invalid"
            fill="green-white"
            textTransform="none"
            fontWeight="500"
            fontSize="16"
            class="the-wizard-import__unlock-button"
            @click="importMnemonic"
          >
            Unlock Wallet
          </e-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * Components:  Wizard, Private key
 *
 * @version 1.0.2
 */
import EBigNumber from '@/components/EBigNumber';
import EIcon from '@/components/EIcon';
import ETextInput from '@/components/ETextInput';
import ETextArea from '@/components/ETextArea';
import EButton from '@/components/EButton';
import EUploadFiles from '@/components/EUploadFiles';
import generateAddressesFromSeed from '@/util/generateAddressesFromSeed';

import { validationMixin } from 'vuelidate';
import generateAddressesFromKeyStore from '@/util/generateAddressesFromKeyStore';
import { required } from 'vuelidate/lib/validators';
import { validPrivateKey, mnemonicPhrase } from '@/util/validators';

export default {
  name: 'the-wizard-import',

  data() {
    return {
      selectedSourceType: 'privateKey',
      privateKey: '',
      keystore: {
        text: '',
        password: '',
        error: '',
      },
      mnemonicPhrase: '',
    };
  },

  validations: {
    privateKey: {
      required,
      validPrivateKey,
    },

    keystore: {
      text: {
        required,
      },
      password: {
        required,
      },
    },

    mnemonicPhrase: {
      required,
      mnemonicPhrase,
    },
  },

  methods: {
    selectSourceType(selectedSourceType) {
      this.selectedSourceType = selectedSourceType;
    },

    onKeystoreFileUpload(file) {
      if (file) {
        const reader = new window.FileReader();
        reader.onload = (e) => {
          this.keystore.text = e.target.result;
        };
        reader.readAsText(file);
      }
    },

    importPrivateKey() {
      this.importOrSetWallet({
        privateKey: this.privateKey,
      });
    },

    importKeystore() {
      let wallet;
      try {
        wallet = generateAddressesFromKeyStore(this.keystore.text, this.keystore.password);
      } catch (error) {
        this.keystore.error = error.message; // TODO: notify on error
      }

      if (wallet) {
        this.importOrSetWallet(wallet);
      }
    },

    importMnemonic() {
      const wallet = generateAddressesFromSeed(this.mnemonicPhrase);
      this.importOrSetWallet(wallet);
    },

    importOrSetWallet(wallet) {
      this.$store.dispatch('wallets/importWallet', wallet);
      this.$emit('next');
    },
  },

  components: {
    EBigNumber,
    EIcon,
    ETextInput,
    ETextArea,
    EButton,
    EUploadFiles,
  },

  mixins: [validationMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wizard-import {
  $this: &;

  padding: 10px 0;

  &__switch {
    display: flex;
    justify-content: center;
  }

  &__switch-item {
    flex: 0 0 auto;
    height: 35px;
    border: 2px solid rgba(31, 238, 188, .4);
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 30px;
    font-size: 12px;
    font-weight: 500;
    color: #fff;
    text-transform: uppercase;
    cursor: pointer;
    margin-left: -2px;
    position: relative;

    &.is-selected {
      background: #00E997;
      border-color: #00E997;
      color: #2429CA;
      z-index: 2;

      &::after {
        content: '';
        display: block;
        position: absolute;
        bottom: -9px;
        left: calc(50% - 3px);
        width: 0;
        height: 0;
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        border-top: 7px solid #00E997;
      }
    }

    &:first-child {
      border-radius: 20px 0 0 20px;
    }

    &:last-child {
      border-radius: 0 20px 20px 0;
    }
  }

  &__content {
    margin: 24px auto 0;
    width: 100%;
    max-width: 428px;
  }

  &__content-description {
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    color: #fff;
  }

  &__input-wrapper {
    margin: 21px 0 16px;
    position: relative;
    display: flex;
    align-items: center;

    .e-text-input {
      flex: 1 1 auto;
    }

    .e-text-input__input {
      padding: 0 14px;

      &::placeholder {
        color: #333;
        opacity: .3;
        font-size: 12px;
      }
    }

    .e-text-input__box {
      height: 58px;
      border-color: transparent;
    }

    .e-text-input_size-m .e-text-input__box {
      width: 100%;
    }
  }

  &__input-label {
    font-size: 13px;
    line-height: 17px;
    font-weight: 500;
    color: #fff;
    width: 60px;
    text-align: right;
    position: absolute;
    top: 50%;
    left: -20px;
    transform: translate(-100%, -50%);
  }

  &__unlock {
    width: calc(100% - 10px);
    margin: 0 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__unlock-button-wrapper {
    .e-button {
      padding: 0 32px;
    }
  }

  &__unlock-description,
  &__unlock-description-keystore {
    margin-right: 20px;
    font-size: 13px;
    line-height: 16px;
    letter-spacing: -.25px;
    font-weight: 500;
    color: #fff;
    opacity: .4;
  }

  &__unlock-description-keystore {
    opacity: 1;
  }

  &__upload {
    position: relative;
  }

  &__upload-label {
    position: absolute;
    right: -15px;
    top: 50%;
    transform: translate(100%, -50%);

    color: #81A8F7;
    opacity: .5;
    font-size: 10px;
    font-weight: 500;
    text-transform: uppercase;
  }

  &__keystore-password {
    position: relative;
    margin: 12px 0 16px;
  }

  &__keystore-label {
    font-size: 13px;
    line-height: 17px;
    font-weight: 500;
    color: #fff;
    width: 60px;
    text-align: right;
    position: absolute;
    top: 50%;
    left: -20px;
    transform: translate(-100%, -50%);
  }

  &__keystore-input {
    .e-text-input {
      flex: 1 1 auto;
    }

    .e-text-input__input {
      padding: 0 14px;

      &::placeholder {
        color: #333;
        opacity: .3;
        font-size: 12px;
      }
    }

    .e-text-input__box {
      height: 58px;
      border-color: transparent;
    }

    .e-text-input_size-m .e-text-input__box {
      width: 100%;
    }
  }

  &__mnemonicPhrase-input {
    margin: 0 0 16px;
    position: relative;

    .e-text-area__left-outer-icon-wrap {
      display: none;
    }

    .e-text-area__box {
      width: 100%;
      height: 127px;
      border-color: transparent;
    }

    .e-text-area__input {
      padding-top: 12px;
    }
  }

  &__mnemonicPhrase-label {
    font-size: 13px;
    line-height: 17px;
    font-weight: 500;
    color: #fff;
    width: 60px;
    text-align: right;
    position: absolute;
    top: 50%;
    left: -20px;
    transform: translate(-100%, -50%);
  }

  &__mnemonicPhrase-description {
    font-size: 10px;
    text-transform: uppercase;
    font-weight: 500;
    opacity: .5;
    color: #81A8F7;
    position: absolute;
    top: 50%;
    right: -20px;
    transform: translate(100%, -50%);
  }
}
</style>



