<template>
  <div class="the-main-news">
    <div class="the-main-news__logos">
      <a href="https://github.com/dmex-app/help-docs" class="the-main-news__logo the-main-news__logo-github" target="_blank">
        <e-icon :src="iconLogoGithub"/>
      </a>
      <a href="https://twitter.com/DMEX_APP" class="the-main-news__logo the-main-news__logo-twitter" target="_blank">
        <e-icon :src="iconLogoTwitter"/>
      </a>
    </div>

    <div class="the-main-news__heading">
      <div class="the-main-news__heading-main">Follow our <span class="the-main-news__heading-highlight">Story</span></div>
      <div class="the-main-news__heading-description">We started 1 year ago. Every day is a new challange. Check out our latest news. Follow us everywhere to stay tuned.</div>
    </div>

    <div class="the-main-news__slider" v-if="adaptiveVersionFeatures">
      <swiper :options="SWIPER_OPTIONS">
        <swiper-slide>
          <router-link :to="{ name: 'futures-contract' }" class="the-main-news__list-item">
            <div class="the-main-news__list-date">
              June 12, 2019
            </div>
            <div class="the-main-news__list-headline">
              The first Futures Contract has officially launched on DMEX!
            </div>
            <div class="the-main-news__list-text">
              Symbol: ETHUSD
              <br>
              Margin: 5x
              <br>
              Expires: 1 July
            </div>
            <div class="the-main-news__list-readmore">
              Read more
              <div class="the-main-news__list-readmore-icon">
                <e-icon :src="iconArrow"/>
              </div>
            </div>
          </router-link>
        </swiper-slide>
        <swiper-slide>
          <router-link :to="{ name: 'dark-mode' }" class="the-main-news__list-item">
            <div class="the-main-news__list-date">
              March 04, 2019
            </div>
            <div class="the-main-news__list-headline">
              Dark Mode now available
            </div>
            <div class="the-main-news__list-text">
              The possibility to switch between Light and Dark Mode is now live on the UI.
            </div>
            <div class="the-main-news__list-readmore">
              Read more
              <div class="the-main-news__list-readmore-icon">
                <e-icon :src="iconArrow"/>
              </div>
            </div>
          </router-link>
        </swiper-slide>
        <swiper-slide>
          <router-link :to="{ name: 'tradingview' }" class="the-main-news__list-item">
            <div class="the-main-news__list-date">
              December 14, 2018
            </div>
            <div class="the-main-news__list-headline">
              TradingView Chart
            </div>
            <div class="the-main-news__list-text">
              The TradingView chart is now integrated and live. This change should considerably improve your trading experience.
            </div>
            <div class="the-main-news__list-readmore">
              Read more
              <div class="the-main-news__list-readmore-icon">
                <e-icon :src="iconArrow"/>
              </div>
            </div>
          </router-link>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>

    <div class="the-main-news__list" v-else>
      <router-link :to="{ name: 'futures-contract' }" class="the-main-news__list-item">
        <div class="the-main-news__list-date">
          June 12, 2019
        </div>
        <div class="the-main-news__list-headline">
          The first Futures Contract has officially launched on DMEX!
        </div>
        <div class="the-main-news__list-text">
          Symbol: ETHUSD
          <br>
          Margin: 5x
          <br>
          Expires: 1 July
        </div>
        <div class="the-main-news__list-readmore">
          Read more
          <div class="the-main-news__list-readmore-icon">
            <e-icon :src="iconArrow"/>
          </div>
        </div>
      </router-link>
      <router-link :to="{ name: 'dark-mode' }" class="the-main-news__list-item">
        <div class="the-main-news__list-date">
          March 04, 2019
        </div>
        <div class="the-main-news__list-headline">
          Dark Mode now available
        </div>
        <div class="the-main-news__list-text">
          The possibility to switch between Light and Dark Mode is now live on the UI.
        </div>
        <div class="the-main-news__list-readmore">
          Read more
          <div class="the-main-news__list-readmore-icon">
            <e-icon :src="iconArrow"/>
          </div>
        </div>
      </router-link>
      <router-link :to="{ name: 'tradingview' }" class="the-main-news__list-item">
        <div class="the-main-news__list-date">
          December 14, 2018
        </div>
        <div class="the-main-news__list-headline">
          TradingView Chart
        </div>
        <div class="the-main-news__list-text">
          The TradingView chart is now integrated and live. This change should considerably improve your trading experience.
        </div>
        <div class="the-main-news__list-readmore">
          Read more
          <div class="the-main-news__list-readmore-icon">
            <e-icon :src="iconArrow"/>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import debounce from 'lodash/debounce';

import 'swiper/dist/css/swiper.css';
import { swiper, swiperSlide } from 'vue-awesome-swiper';

import iconLogoReddit from '@/icons/e-icon__logo-reddit.svg';
import iconLogoTwitter from '@/icons/e-icon__logo-twitter.svg';
import iconLogoGithub from '@/icons/e-icon__logo-github.svg';
import iconArrow from '@/icons/e-icon_arrow.svg';

import EIcon from '@/components/EIcon';

const SWIPER_OPTIONS = {
  slidesPerView: 'auto',
  centeredSlides: true,
  spaceBetween: 20,
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },
};

export default {
  name: 'the-main-news',

  data() {
    return {
      iconLogoReddit,
      iconLogoTwitter,
      iconLogoGithub,
      iconArrow,
      adaptiveVersionFeatures: false,
    };
  },

  created() {
    this.onResize = debounce(this.onResize.bind(this), 200);
    this.SWIPER_OPTIONS = SWIPER_OPTIONS;
  },

  mounted() {
    this.onResize();
  },

  computed: {
    ...mapGetters({
      windowSize: 'ui/windowSize',
    }),
  },

  methods: {
    onResize() {
      this.adaptiveVersionFeatures = this.windowSize.width < 768;
    },
  },

  watch: {
    windowSize: {
      deep: true,
      handler() {
        this.onResize();
      },
    },
  },

  components: {
    EIcon,
    swiper,
    swiperSlide,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-main-news {
  $this: &;

  position: relative;
  padding: 60px 0 24px;
  background-image: radial-gradient(#f2f2f2, #ececec);

  @include r(992) {
    padding-top: 150px;
    background: #f2f2f2;
  }

  @include r(640) {
    padding-bottom: 0;
  }

  &__logos {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }

  &__logo {
    display: block;
    padding: 0 34px;
    margin-bottom: 34px;
    transition: transform .15s ease-in-out;

    &:hover {
      transform: scale(1.04);
    }
  }

  &__logo-reddit {
    height: 40px;

    .e-icon {
      width: 124px;

      @include r(600) {
        width: 100px;
      }
    }
  }

  &__logo-github {
    height: 50px;

    .e-icon {
      width: 50px;
      height: 50px;
    }
  }

  &__logo-twitter {
    height: 50px;

    .e-icon {
      width: 50px;

      @include r(600) {
        width: 40px;
        height: 40px;
      }
    }
  }

  &__logo-bitcointalk {
    img {
      width: 100%;
      max-width: 144px;
    }
  }

  &__heading {
    margin: 0 auto;
    width: 100%;
    max-width: 670px;
    padding: 40px 30px 40px;
    text-align: center;
    position: relative;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;

    @include r(640) {
      padding: 10px 30px 25px;
    }

    &:after {
      content: '';
      display: block;
      position: absolute;
      left: calc(50% - 43px);
      width: 87px;
      height: 3px;
      background: #0123E7;
      bottom: 0;
    }

    @include r(640) {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__heading-main {
    font-size: 36px;
    font-weight: 500;
    line-height: 44px;
    letter-spacing: -.4px;
    padding-bottom: 10px;

    @include r(640) {
      font-size: 32px;
      line-height: 40px;
    }
  }

  &__heading-highlight {
    color: #1F2EE9;
  }

  &__heading-description {
    font-family: 'DIN Pro';
    font-weight: 300;
    font-size: 22px;
    line-height: 33px;
    color: #575A74;

    @include r(640) {
      font-size: 18px;
      line-height: 26px;
    }
  }

  &__list {
    width: 100%;
    max-width: 1224px;
    margin: 50px auto 0;
    padding: 0 30px;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;

    @include r(640) {
      padding: 0 15px;
    }
  }

  &__list-item {
    cursor: pointer;
    flex: 0 0 calc(100% / 3 - 15px);
    margin: 0 11px 22px;
    background: #fff;
    box-shadow: 0 5px 9px rgba(31, 46, 233, .08);
    transition: all .2s ease-in-out;
    padding: 34px 24px;
    box-sizing: border-box;
    border-radius: 5px;
    text-decoration: none;
    display: inline-block;

    @include r(767) {
      margin: 0;
    }

    &:nth-child(3n + 1) {
      margin-left: 0;
    }

    &:nth-child(n + 3) {
      margin-right: 0;
    }

    &:hover {
      background: #F5F4F4;
      box-shadow: 0 15px 14px rgba(205, 226, 245, .6);
      transform: translate(0, -5px);

      #{$this}__list-date {
        color: #1F2EE9;
      }
      #{$this}__list-text {
        color: #1F2EE9;

        &::after {
          background-image: linear-gradient(180deg, rgba(238, 238, 238, 0) 0%, #f5f4f4 83%, #f5f4f4 100%);
        }
      }
      #{$this}__list-readmore {
        color: #1F2EE9;
      }
      #{$this}__list-readmore-icon svg path {
        fill: #1F2EE9;
      }
    }
  }

  &__list-date {
    color: #BBC2D0;
    font-family: 'DIN Pro';
    font-weight: normal;
    font-size: 13px;
    letter-spacing: .5px;
    line-height: 15px;
    padding-bottom: 20px;
    text-transform: uppercase;
    transition: color .15s ease-in-out;
  }

  &__list-headline {
    color: #1F2EE9;
    font-size: 23px;
    line-height: 25px;
    font-weight: 500;
    padding-bottom: 24px;
    transition: color .15s ease-in-out;
    min-height: 74px;

    @include r(640) {
      padding-bottom: 8px;
    }
  }

  &__list-text {
    position: relative;
    font-family: 'DIN Pro';
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    color: #000;
    opacity: .6;
    height: 144px;
    overflow: hidden;
    transition: color .15s ease-in-out;
    margin-bottom: 16px;
    z-index: 1;

    @include r(640) {
      font-size: 13px;
      font-weight: normal;
    }

    &::after {
      content: '';
      left: 0;
      bottom: -8px;
      width: 100%;
      height: 68px;
      z-index: 2;
      position: absolute;
      background-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #ffffff 83%, #ffffff 100%);
    }
  }

  &__list-readmore {
    color: #070337;
    font-size: 16px;
    font-weight: 500;
    display: flex;
    justify-content: space-between;
    transition: color .15s ease-in-out;
  }

  &__list-readmore-icon {
    width: 12px;
    height: 15px;
    transform: rotate(-90deg) translate(-7px, 0);

    svg path {
      fill: #00E698;
      transition: fill .15s ease-in-out;
    }
  }

  &__slider {
    position: relative;
    margin-top: 77px;

    @include r(640) {
      margin-top: 42px;
    }

    .swiper-container {
      overflow: visible;
    }

    .swiper-slide {
      width: 50%;
      padding-bottom: 30px;

      @include r(599) {
        width: 80%;
        padding-bottom: 10px;
      }
    }

    .swiper-pagination {
      bottom: 9px;
      position: relative;
      z-index: 3;
    }

    .swiper-pagination-bullet {
      width: 5px;
      height: 5px;
      margin: 0 7px;
      transition: .3s ease;
      background-color: $color-blue;
      outline: none;
    }

    .swiper-pagination-bullet-active {
      transform: scale(1.3);
    }
  }
}
</style>



