<template>
  <div class="the-main-investing">
    <div class="the-main-investing__wrapper">
      <div class="the-main-investing__promo-wrapper">
        <div class="the-main-investing__promo the-main-investing__promo-right">
          <div class="the-main-investing__promo-content">
            <div class="the-main-investing__promo-heading">
              <span class="the-main-investing__promo-highlight">Leveraged</span>
              (Margin)
              <span class="the-main-investing__promo-highlight">Trading</span>
            </div>
            <div class="the-main-investing__promo-description">
              Trade Bitcoin/Litecoin/Monero and other crypto assets with up to 20x Leverage using ETH or TUSD. Next level decentralized trading is waiting for you.
            </div>
            <div class="the-main-investing__main-content-enter-exchange">
              <router-link :to="{ name: 'futures' }">
                <e-button
                  fill="green-blue"
                  textTransform="none"
                  fontWeight="500"
                  fontSize="16"
                  class="page-index-new__main-content-exchange-button"
                >
                  Go to trading
                </e-button>
              </router-link>
            </div>
          </div>
          <div class="the-main-investing__promo-icon the-main-investing__promo-icon-ico-investing">
            <e-icon :src="iconMarginTrading"/>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * Component:  ICO  
 *
 * @version 1.0.0
 */

import { mapState, mapGetters } from 'vuex';

import { validationMixin } from 'vuelidate';
import { required, email } from 'vuelidate/lib/validators';
import { routesMixin } from '@/util/mixins';
import { POPUP_TOKEN_CATALOGUE } from '@/store/constants';
import { subscribeEmail } from '@/api/backend';
import { ASYNC_STATES } from '@/util/constants';
import doScrolling from '@/util/doScrolling';

// Components
import ETokenIcon from '@/components/ETokenIcon';
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';

// Icons
import iconIcoInvesting from '@/icons/e-icon__ico-investing.svg';
import iconMarginTrading from '@/icons/e-icon__margin-trading.svg';
import iconLetter from '@/icons/e-icon__letter.svg';

const TOKEN_CONTRACTS = process.env.PROD_ENV === 'main'
  ? [
    '0xE41d2489571d322189246DaFA5ebDe1F4699F498',
    '0xA15C7Ebe1f07CaF6bFF097D8a589fb8AC49Ae5B3',
    '0x2C974B2d0BA1716E644c1FC59982a89DDD2fF724',
    '0x419D0d8BdD9aF5e606Ae2232ed285Aff190E711b',
    '0x1460a58096d80a50a2F1f956DDA497611Fa4f165',
    '0x05f4a42e251f2d52b8ed15E9FEdAacFcEF1FAD27',
    '0x6c6EE5e31d828De241282B9606C8e98Ea48526E2',
    '0xd26114cd6EE289AccF82350c8d8487fedB8A0C07',
    '0xA4e8C3Ec456107eA67d3075bF9e3DF3A75823DB0',
    '0xE5Dada80Aa6477e85d09747f2842f7993D0Df71C',
  ]
  : [
    '0x99F3583d7c07f666B3cDcfaB9136fC520D3f5E43',
    '0xf8ec30153aA03783591f3ce6285017eE83AC72dA',
    '0xCe50c4c3006f4728845E1cE52ABb46F5D01Ef8c0',
    '0xB138eFBDcbCC457B700977d7e09BCBB195048D49',
    '0x41951fF00c9a42A5944f5E1281D1e534c1B3b93F',
    '0x49fc784b3A8D4bDAf448488f6FF64F92b8B2b4Ce',
    '0x330DF16E4CC4032019096A3de120C10bDfECae60',
    '0x2dF70458939815976fb425E6f65D3C470Bd78e04',
    '0x20AaF043c75C04B2DEea6CDf88246f362c9FF9dE',
    '0x378Ac908af415cE6BEc0a912C0383D4D52923c59',
  ];

export default {
  name: 'the-main-investing',

  data() {
    return {
      /**
       *     
       */
      userEmail: '',
      subscribeStatus: ASYNC_STATES.INITIAL,

      // Icons
      iconIcoInvesting,
      iconMarginTrading,
      iconLetter,
    };
  },

  computed: {
    ...mapState({
      tokensLoaded: state => state.tokens.tokensLoaded,
    }),

    ...mapGetters({
      getToken: 'tokens/tokenByContractSimple',
      firstContractHash: 'futuresActiveContract/firstContractHash',
    }),

    submitPending() {
      return this.subscribeStatus === ASYNC_STATES.PENDING;
    },

    wasSubmitSuccessful() {
      return this.subscribeStatus === ASYNC_STATES.SUCCESS;
    },

    wasSubmitError() {
      return this.subscribeStatus === ASYNC_STATES.ERROR;
    },

    isValidationOrSubmitError() {
      return this.$v.$error || this.wasSubmitError;
    },

    tokens() {
      return TOKEN_CONTRACTS.map(item => this.getToken(item));
    },
  },

  validations: {
    userEmail: {
      required,
      email,
    },
  },

  async mounted() {
    /**
     *      
     */
    await Promise.all([
      this.$store.dispatch('tokens/loadTokens'),
    ]);

    const success = this.$store.state.tokens.tokensLoaded;
    if (success) {
      this.$emit('loaded');
    }
  },

  methods: {
    /**
     *     
     */
    handleOpenTokenCatalog() {
      this.$router.push(this.$popupRoute(POPUP_TOKEN_CATALOGUE));
    },

    /**
     *   
     *
     *   -    
     */
    async handleSubscribe() {
      this.$v.userEmail.$touch();
      if (!this.$v.$error) {
        this.subscribeStatus = ASYNC_STATES.PENDING;
        try {
          await subscribeEmail(this.userEmail);
          this.subscribeStatus = ASYNC_STATES.SUCCESS;
        } catch (e) {
          this.subscribeStatus = ASYNC_STATES.ERROR;
        }

        if (this.wasSubmitSuccessful) {
          this.userEmail = '';
          const emailInput = this.$refs.email;
          if (emailInput) {
            emailInput.blur();
          }
          this.$v.$reset();
        }
      }
    },

    /**
     *     
     */
    handleScrollToSearchInput() {
      doScrolling('#page-index-new__wrapp', '#page-index-new__search-input', 1000);
      document.getElementById('page-index-new__search-input').focus();
    },
  },

  components: {
    ETokenIcon,
    EButton,
    EIcon,
  },

  mixins: [validationMixin, routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-main-investing {
  $this: &;

  padding: 135px 0 170px;
  background: radial-gradient(ellipse at center, #091877 0%, #071566 85%, #071566 100%);
  overflow: hidden;
  position: relative;

  @include r(767) {
    padding: 60px 0 30px;
  }

  @include r(640) {
    background: #0b1e8f;
  }

  &::after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    z-index: 0;
    width: 100%;
    height: 1100px;
    background: linear-gradient(to bottom, rgba(5,4,26,.7) 0%,rgba(5,4,26,.35) 40%,rgba(5,4,26,0) 100%);
  }

  &:before {
    content: '';
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    z-index: 0;
    bottom: -140px;
    width: 100%;
    max-width: 1600px;
    height: 180px;
    background: #030C52;
    border-radius: 50%;
    filter: blur(20px);

    @include r(640) {
      display: none;
    }
  }

  &__wrapper {
    position: relative;
    z-index: 1;
    max-width: 1310px;
    padding: 0 30px;
    margin: 0 auto;
    box-sizing: border-box;
    color: #fff;

    @include r(640) {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__promo {
    display: flex;
    justify-content: space-between;

    @include r(767) {
      flex-wrap: wrap;
    }
  }

  &__promo-content {
    flex: 0 0 480px;

    @include r(992) {
      flex: 0 0 50%;
    }

    @include r(767) {
      flex: 0 0 100%;
      text-align: center;
    }
  }

  &__promo-heading {
    font-size: 53px;
    font-weight: 500;
    letter-spacing: -1px;
    line-height: 61px;
    padding-bottom: 32px;

    @include r(992) {
      font-size: 40px;
      line-height: 48px;
    }

    @include r(640) {
      font-size: 36px;
    }
  }

  &__promo-highlight {
    color: #00E997;
  }

  &__promo-description {
    font-size: 22px;
    font-weight: normal;
    line-height: 33px;
    padding-bottom: 30px;
    max-width: 480px;

    @include r(992) {
      font-size: 18px;
      line-height: 26px;
    }

    @include r(767) {
      max-width: 100%;
    }

    @include r(640) {
      font-weight: 300;
    }
  }

  &__promo-buttons {
    margin-bottom: -15px;

    .e-button {
      margin-right: 15px;
      margin-bottom: 15px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__main-content-enter-exchange {
    margin-bottom: -15px;

    .e-button {
      padding-left: 40px;
      padding-right: 40px;
    }
  }

  &__main-content-exchange-button {
    height: 64px !important;
    font-size: 20px !important;
    letter-spacing: 0 !important;
    font-weight: 500 !important;
    padding-right: 32px !important;

    .e-icon {
      width: 38px !important;
      height: 36px !important;
      margin-right: 12px !important;

      svg path {
        fill: #020D5F;
        transition: fill .15s ease-in-out;
      }
    }

    &:hover {
      .e-icon svg path {
        fill: #fff;
      }
    }
  }

  &__promo-icon {
    .e-icon svg {
      @include r(767) {
        position: relative;
      }
    }
  }

  &__promo-icon-ico-investing {
    width: 488px;
    height: 310px;
    margin-right: 80px;

    @include r(1050) {
      margin-right: 0;
      margin-left: 30px;
    }

    @include r(992) {
      width: auto;
      flex: 1;
    }

    @include r(767) {
      margin: 50px 0 0 0;
      width: auto;
      height: auto;
      flex: 0 0 100%;
      display: flex;
      justify-content: center;

      .e-icon {
        max-width: 350px;
      }
    }
  }

  &__promo-icon-margin-trading {
    width: 494px;
    height: 429px;
    margin-top: 40px;
    margin-left: 50px;

    @include r(1050) {
      margin-right: 30px;
      margin-left: 0;
    }

    @include r(767) {
      margin: 50px 0 0;
      width: 100%;
      height: auto;
      display: flex;
      justify-content: center;
      order: 2;

      .e-icon {
        max-width: 350px;
      }
    }
  }

  &__promo-notified-label {
    font-size: 22px;
    line-height: 33px;
    font-weight: normal;
    color: #00E795;
    padding-bottom: 12px;
    margin-top: -16px;

    @include r(640) {
      font-weight: 300;
      font-size: 18px;
      line-height: 27px;
    }
  }

  &__promo-notified-form {
    display: flex;

    @include r(767) {
      justify-content: center;
    }

    @include r(640) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__promo-notified-email {
    height: 57px;
    border: 2px solid #16E59D;
    border-right: 0;
    font-size: 18px;
    font-weight: 500;
    color: #29E7A7;
    padding: 0 0 0 58px;
    box-sizing: border-box;
    background-color: transparent;
    transition: opacity .15s ease-in-out;
    opacity: .4;

    @include r(640) {
      width: 100%;
      border-right: 2px solid #16E59D;
    }

    &::placeholder {
      color: #29E7A7;
    }

    &:focus,
    &.is-filled {
      opacity: 1;

      + #{$this}__promo-notified-input-icon {
        opacity: 1;
      }
    }

    @include r(767) {
      padding-left: 16px;
    }
  }

  &__promo-notified-input-wrapper {
    position: relative;

    @include r(640) {
      margin-bottom: 20px;
      width: 100%;
    }
  }

  &__form-info {
    position: absolute;
    bottom: -6px;
    transform: translateY(100%);

    @include r(767) {
      position: unset;
      bottom: unset;
      transform: unset;
      margin-top: 6px;
    }
  }

  &__form-info_success {
    color: #29E7A7;
  }

  &__form-info_error {
    color: $color-red;
  }

  &__promo-notified-input-wrapper_error {
    #{$this}__promo-notified-email {
      border-color: $color-red;
      color: $color-red;
      &::placeholder {
        color: $color-red;
      }
    }

    #e-icon__letter-Fill-4 {
      fill: #b53520;
    }

    #e-icon__letter-Fill-1 {
      fill: $color-red;
    }
  }

  &__promo-notified-submit {
    border-radius: 0;
    height: 57px;

    @include r(640) {
      width: 190px;
      border-radius: 60px;
    }

    &:hover {
      transform: translate(0, 0);
    }
  }

  &__promo-notified-input-icon {
    opacity: .4;
    transition: opacity .15s ease-in-out;
    position: absolute;
    left: 24px;
    top: 23px;
    display: block;
    width: 20px;
    height: 14px;
    cursor: pointer;

    @include r(767) {
      display: none;
    }
  }

  &__token-listed {
    max-width: 970px;
    margin: 0 auto;
  }

  &__token-listed-heading {
    font-size: 36px;
    font-weight: 500;
    letter-spacing: -1px;
    line-height: 61px;
    padding-bottom: 32px;
    text-align: center;

    @include r(767) {
      line-height: 42px;
    }

    @include r(640) {
      padding-bottom: 22px;
    }
  }

  &__token-listed-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &__token-listed-item {
    flex: 0 0 20%;
    display: flex;
    align-items: center;
    margin-bottom: 18px;
    padding: 10px;
    box-sizing: border-box;
    position: relative;
    cursor: pointer;

    @include r(767) {
      flex: 0 0 145px;
    }

    &::before {
      content: '';
      position: absolute;
      z-index: 0;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;
      border-radius: 5px;
      background: linear-gradient(135deg, rgba(40,36,255,1) 0%,rgba(0,24,168,1) 100%);
      opacity: 0;
      transition: opacity .15s ease-in-out;
    }

    &:hover {
      &::before {
        opacity: 1;
      }
    }
  }

  &__token-listed-content {
    position: relative;
    z-index: 1;
  }

  &__token-listed-icon {
    position: relative;
    z-index: 1;
    width: 40px;
    height: 40px;
    margin-right: 15px;
  }

  &__token-listed-token-shortname {
    font-size: 17px;
    color: #fff;
    font-weight: 500;
    letter-spacing: -.5px;
    margin-bottom: -4px;
  }

  &__token-listed-token-fullname {
    font-size: 14px;
    font-weight: 500;
    letter-spacing: -.5px;
    color: #fff;
    opacity: .43;
  }

  &__token-browse-all {
    margin: 40px 0;
    display: flex;
    align-items: center;
    justify-content: center;

    @include r(640) {
      margin: 14px 0;
    }
  }
}
</style>



