<template>
  <the-wizard-wallet-types
    v-if="isWalletTypeSelection"
    v-on="selectWalletHandlers"
  />

  <e-wizard-page
    v-else-if="isNewWalletDetailsDisplayed"
    headline="Congratulations!"
    description="New wallet generated successfully"
    back-label="Connect a different wallet"
    @back="reset"
  >
    <the-wizard-create-new-wallet-congratulations @next="nextStep"/>
  </e-wizard-page>

  <e-wizard-page
    v-else-if="isImportWalletSelection"
    headline="Unlock your ETH wallet"
    description="Import your wallet using one of these options"
    back-label="Connect a different wallet"
    @back="reset"
  >
    <the-wizard-import @next="nextStep"/>
  </e-wizard-page>
</template>

<script>
import EIcon from '@/components/EIcon';
import EWizardPage from '@/components/EWizardPage';
import TheWizardCreateNewWalletCongratulations from '@/containers/blocks/TheWizardCreateNewWalletCongratulations';
import TheWizardWalletTypes from '@/containers/blocks/TheWizardWalletTypes';
import TheWizardImport from '@/containers/blocks/TheWizardImport';
import { CREATE, IMPORT, METAMASK, LEDGER } from '@/containers/blocks/TheWizard';


export default {
  name: 'the-wizard-step-wallet',

  data() {
    return {
      selectedWalletOption: null,
    };
  },

  computed: {
    /* eslint-disable vue/no-side-effects-in-computed-properties */
    selectWalletHandlers() {
      return {
        [CREATE]: () => {
          this.selectedWalletOption = CREATE;
        },
        [IMPORT]: () => {
          this.selectedWalletOption = IMPORT;
        },
        [METAMASK]: () => {
          this.selectedWalletOption = METAMASK;
          this.nextStep();
        },
        [LEDGER]: () => {
          this.selectedWalletOption = LEDGER;
          this.nextStep();
        },
      };
    },
    /* eslint-enable vue/no-side-effects-in-computed-properties */

    isWalletTypeSelection() {
      return this.selectedWalletOption === null;
    },

    isNewWalletDetailsDisplayed() {
      return this.selectedWalletOption === CREATE;
    },

    /**
     * Special interface for import wallet.
     */
    isImportWalletSelection() {
      return this.selectedWalletOption === IMPORT;
    },
  },

  methods: {
    reset() {
      this.selectedWalletOption = null;
    },

    nextStep() {
      this.$emit('update:selected-wallet-option', {
        option: this.selectedWalletOption,
      });
    },
  },

  components: {
    EWizardPage,
    EIcon,
    TheWizardCreateNewWalletCongratulations,
    TheWizardWalletTypes,
    TheWizardImport,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wizard-step-wallet {
}
</style>



