<template>
  <e-wizard-page
    v-if="depositInitialization && selectedNewWallet"
    headline="Deposit Funds"
    description="What do you want to deposit?"
    @back="back"
  >
    <the-wizard-new-deposit
      @deposit="setDepositInProgress"
    />
  </e-wizard-page>

  <e-wizard-page
    v-else-if="depositInitialization && (selectedImportedWallet || selectedMetaMaskWallet || selectedLedgerWallet)"
    headline="Deposit Funds"
    description="What do you want to deposit?"
    @back="back"
  >
    <the-wizard-deposit-connected
      @deposit="handleSubmitDeposit"
    />
  </e-wizard-page>

  <e-wizard-page
    v-else-if="depositNeedConfirmation && selectedMetaMaskWallet"
    headline="Deposit Funds"
    description="What do you want to deposit?"
    @back="back"
  >
    <the-wizard-metamask-transaction
      @confirmed="setDepositInProgress"
      @canceled="setDepositInitialization"
    />
  </e-wizard-page>

  <e-wizard-page
    v-else-if="depositNeedConfirmation && selectedLedgerWallet"
    headline="Deposit Funds"
    description="What do you want to deposit?"
    @back="back"
  >
    <the-wizard-ledger-transaction
      @confirmed="setDepositInProgress"
      @canceled="setDepositInitialization"
    />
  </e-wizard-page>


  <e-wizard-page
    v-else-if="depositInProgress || depositSuccess"
    @back="back"
  >
    <the-wizard-status/>
  </e-wizard-page>
</template>

<script>
import EWizardPage from '@/components/EWizardPage';
import TheWizardNewDeposit from '@/containers/blocks/TheWizardNewDeposit';
import TheWizardDepositConnected from '@/containers/blocks/TheWizardDepositConnected';
import TheWizardStatus from '@/containers/blocks/TheWizardStatus';
import TheWizardMetamaskTransaction from '@/containers/blocks/TheWizardMetamaskTransaction';
import TheWizardLedgerTransaction from '@/containers/blocks/TheWizardLedgerTransaction';
import { CREATE, IMPORT, METAMASK, LEDGER } from '@/containers/blocks/TheWizard';

// TODO: Ledger
export default {
  name: 'the-wizard-step-deposit',

  props: {
    selectedWalletOption: {
      type: String,
      required: true,
      validator: v => [CREATE, IMPORT, METAMASK, LEDGER].includes(v),
    },
  },

  data() {
    return {
      depositStatus: 'init',
    };
  },

  computed: {
    selectedNewWallet() {
      return this.selectedWalletOption === CREATE;
    },

    selectedImportedWallet() {
      return this.selectedWalletOption === IMPORT;
    },

    selectedMetaMaskWallet() {
      return this.selectedWalletOption === METAMASK;
    },

    selectedLedgerWallet() {
      return this.selectedWalletOption === LEDGER;
    },

    depositInitialization() {
      return this.depositStatus === 'init';
    },

    depositNeedConfirmation() {
      return this.depositStatus === 'need confirmation';
    },

    depositInProgress() {
      return this.depositStatus === 'in progress';
    },

    depositSuccess() {
      return this.depositStatus === 'success';
    },
  },

  created() {
    Object.assign(this, {
      IMPORT,
      METAMASK,
    });
  },

  methods: {
    handleSubmitDeposit() {
      const handler = {
        [CREATE]: this.setDepositInProgress,
        [IMPORT]: this.setDepositInProgress,
        [METAMASK]: this.setDepositNeedConfirmation,
        [LEDGER]: this.setDepositNeedConfirmation,
      }[this.selectedWalletOption];
      handler.call(this);
    },

    setDepositInitialization() {
      this.depositStatus = 'init';
    },

    setDepositNeedConfirmation({ tokenAddress } = {}) {
      this.depositStatus = 'need confirmation';
      this.depositTokenAddress = tokenAddress;
    },

    setDepositInProgress({ tokenAddress } = {}) {
      this.depositStatus = 'in progress';
      this.depositTokenAddress = tokenAddress;
    },

    back() {
      this.$emit('back');
    },
  },
  components: {
    EWizardPage,
    TheWizardNewDeposit,
    TheWizardStatus,
    TheWizardDepositConnected,
    TheWizardMetamaskTransaction,
    TheWizardLedgerTransaction,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wizard-step-deposit {
}
</style>



