<template>
  <div class="e-wizard-page">
    <div
      class="e-wizard-page__return"
      @click="$emit('back')"
    >
      <div class="e-wizard-page__return-icon">
        <e-icon :src="$options.icons.iconBack" />
      </div>

      <div class="e-wizard-page__return-text">
        {{ backLabel }}
      </div>
    </div>

    <div class="e-wizard-page__wrapper">
      <div
        v-if="headline"
        class="e-wizard-page__heading"
      >
        <div class="e-wizard-page__headline">{{ headline }}</div>
        <div class="e-wizard-page__description">{{ description }}</div>
      </div>

      <slot/>
    </div>
  </div>
</template>

<script>
import EIcon from '@/components/EIcon';

import iconBack from '@/icons/e-icon__back.svg';

export default {
  name: 'e-wizard-page',

  props: {
    headline: String,
    description: String,
    backLabel: {
      type: String,
      default: 'Connect a different wallet',
    },
  },

  icons: {
    iconBack,
  },

  components: { EIcon },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-wizard-page {
  $this: &;
  position: relative;

  &__return {
    position: absolute;
    left: 70px;
    top: 50%;
    transform: translate(0, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 70px;
    cursor: pointer;

    &:hover {
      #{$this}__return-text {
        color: #fff;
      }
    }
  }

  &__return-icon {
    width: 53px;
    height: 53px;
    fill: #00E997;
  }

  &__return-text {
    font-size: 10px;
    font-weight: 500;
    color: rgba(129, 168, 247, .5);
    transition: color .15s ease-in-out;
    text-align: center;
    text-transform: uppercase;
  }

  &__wrapper {
    width: 100%;
    max-width: 780px;
    padding: 28px 38px 40px;
    box-sizing: border-box;
    margin: 0 auto;
    border-radius: 3px;
    position: relative;
    background: radial-gradient(ellipse at center, #0285e6 0%,rgba(26,30,176,1) 100%);
  }

  &__heading {
    text-align: center;
    padding-bottom: 8px;
  }

  &__headline {
    font-size: 19px;
    font-weight: 500;
    line-height: 24px;
    color: #fff;
  }

  &__description {
    font-size: 10px;
    font-weight: 500;
    line-height: 21px;
    color: #81A8F7;
    opacity: .5;
    text-transform: uppercase;
  }
}
</style>



